<template>
  <div
    class="menu"
    :style="menuStyle"
    refs="menu">
    <div @click="toggleGrid" :class="{active: isGridLineVisible}" class="fa fa-table">
      <span>网格</span>
    </div>
    <!--<div @mousedown="onUndo" class="dyeditor-icon-cexiao"><span>撤销</span></div>-->
    <!--<div @mousedown="onRedo" class="dyeditor-icon-chongzuo"><span>重做</span></div>-->
  </div>
</template>

<script>
  import * as T from '../store/types';
  import { mapGetters } from 'vuex';


  export default {
    name: 'canvas-side-menu',
    computed: {
      ...mapGetters({
        canvasSizePx: T.GET_SCALED_CANVAS_SIZE_PX,
        isGridLineVisible: T.IS_GRID_LINE_VISIBLE,
      }),
      menuStyle() {
        const MENU_HEIGHT = 120;
        const V_GAP = 10;
        var $ct = $('.canvas-container'),
          ctW = $ct.width(),
          ctH = $ct.height(),
          canvasPxWidth = parseFloat(this.canvasSizePx.width),
          canvasPxHeight = parseFloat(this.canvasSizePx.height),
          marginLeft = canvasPxWidth / 2 + V_GAP,
          top = Math.min(
            canvasPxHeight / 2 - MENU_HEIGHT / 2 + 30,
            ctH / 2 + 30
          );
        return marginLeft > .5 * ctW - 20
          ? { top: `${top}px`, right: `${V_GAP}px` }
          : { top: `${top}px`, left: "50%", marginLeft: `${marginLeft}px` };
      },
    },
    methods: {
      toggleGrid: function() {
        this.$store.commit(T.SET_GRID_LINE_VISIBLE, !this.isGridLineVisible);
      },
    }

  };
</script>

<style lang="scss" rel="stylesheet/scss">
  .menu {
    width: 30px;
    position: absolute;
    div {
      position: relative;
      width: 100%;
      height: 30px;
      margin-bottom: 10px;
      line-height: 30px;
      color: rgba(73, 73, 73, 0.8);
      border-radius: 3px;
      opacity: 0.6;
      cursor: pointer;
      text-align: center;
      font-size: 30px;
      &.icon-grid.active {
        opacity: 1;
      }
      &:hover {
        opacity: 1;
        span {
          opacity: 1;
        }
      }
      span {
        opacity: 0;
        font-size: 14px;
        color: #333;
        position: absolute;
        left: 40px;
        width: 33px;
        text-align: left;
      }
    }
  }

</style>



