<template>
  <div class="map-pannel pannel">
    <div class="panel-title border-bottom">地图</div>
    <div class="panel-content">
      <el-form label-position="left" label-width="40px" onsubmit="return false;">
        <el-form-item label="地址">
          <address-input :keyword="keyword" :on-select="selectAddress"></address-input>
        </el-form-item>
        <el-form-item label="大小">
          <span class="zoom-btn" @click="changeZoom(1)"><i class="el-icon-plus"></i></span>
          <span class="zoom-btn" @click="changeZoom(-1)"><i class="el-icon-minus"></i></span>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue';
  import ElForm from 'element-ui';
  import ElInput from 'element-ui';
  import AddressInput from './AddressInput';
  import * as T from '../../store/types';
  import { mapGetters } from 'vuex';

  import {
    MAP_ZOOM_MAX,
    MAP_ZOOM_MIN,
  } from 'constant';


  Vue.use(ElForm);
  Vue.use(ElInput);

  export default {
    components: {
      AddressInput,
    },
    data() {
      return {
        keyword: ''
      };
    },
    computed: {
      ...mapGetters({
        selElemId: T.GET_SEL_ELEM_ID,
        selElem: T.GET_SEL_ELEM,
      }),
      // TODO getters
      selMapElem() {
        const elem = this.selElem;
        return elem && elem.type === 'map' ? elem : null;
      },
    },
    methods: {
      changeZoom(delta) {
        // TODO getter zoom
        let zoom = this.selElem.map.zoom;
        zoom += delta;
        if (zoom >= MAP_ZOOM_MIN && zoom <= MAP_ZOOM_MAX) {
          this.$store.commit(T.UPDATE_SEL_ELEM, {
            map: { zoom }
          })
        }
      },
      selectAddress({ address, location, title }) {
        this.$store.commit(T.UPDATE_SEL_ELEM, {
          map: { address, location, title }
        })
      }
    },
    created(){
      if (this.selMapElem) {
        this.keyword = this.selMapElem.map.title;
      }
    },
    watch: {
      selMapElem(elem) {
        if (elem) {
          console.log('>>>map update this.keyword');
          this.keyword = elem.map.title;
        }
      },
    },
  }
</script>

<style lang="scss" rel="stylesheet/scss">
  $zoomBtnSize: 32px;
  .zoom-btn {
    display: inline-block;
    width: $zoomBtnSize;
    height: $zoomBtnSize;
    line-height: $zoomBtnSize;
    background-color: #fff;
    border-radius: 3px;
    position: relative;
    margin-right: 8px;
    font-size: $zoomBtnSize/2;
    font-weight: 300;
    cursor: pointer;
    text-align: center;
  }
</style>



