<template>
  <div class="canvas-resizer">
    <div class="resizer">
      <span @click="addCanvasHeight(-504)" class="icon fa fa-caret-up up"></span>
      <span :class="{ active: resizing }" class="text">拖动调节页面高度</span>
      <span @click="addCanvasHeight(504)" class="icon fa fa-caret-down down"> </span>
    </div>
  </div>

</template>

<script>
  import Base from './Base';
  import { mapGetters } from 'vuex';
  import * as T from '../store/types';
  const MIN_HEIGHT = 1008; // TODO import from const
  export default {
    mixins: [Base],

    data: function () {
      return {
        resizing: false,
        height: null,
      }
    },
    computed: {
      ...mapGetters({
        canvasSize: T.GET_CANVAS_SIZE,
        scale: T.GET_SCALE_VAL,
      }),
    },
    methods: {
      addCanvasHeight: function (delta) {
        this.height = this.ensureHeight(this.canvasSize.height + delta);
        this.$store.commit(T.SET_CANVAS_SIZE, { height: this.height });
        this.$nextTick(function () {
          window.myScroll.refresh()
        });
      },
      pushCanvasSize () {
        if (this.canvasSize.height != this.height) {
          this.$store.commit(T.SET_CANVAS_SIZE, {
            height: this.height
          });
        }
      },
      ensureHeight (h) {
        return parseInt(Math.max(h, MIN_HEIGHT), 10);
      },
    },
    created(){
      this.height = this.canvasSize.height;
    },
    beforeDestroy () {
      $(this.$el).off('.resizing');
    },
    mounted: function () {
      const me = this;
      let scrollYWhenStart = null;
      let scrollY = null;
      let heightWhenStart = null;

      setTimeout(() => {
        window.myScroll.on("scroll", function () {
          me.resizing && (scrollY = this.y);
        });
      });

      $(this.$el).on("movestart.resizing", function (e) {
        heightWhenStart = me.height;
        scrollYWhenStart = scrollY = window.myScroll.y;
        this.resizing = true;
      });

      $(this.$el).on("move.resizing", function (e) {
        const scale = me.scale;
        const scrollDistance = scrollYWhenStart - scrollY;
        const moveDownDistance = e.pageY - e.startY;
        const deltaModelDistance = (scrollDistance + moveDownDistance) / scale;
        me.height = me.ensureHeight(heightWhenStart + deltaModelDistance);
        me.pushCanvasSize();
        window.myScroll.refresh();
      });

      $(this.$el).on("moveend.resizing", function (e) {
        me.resizing = false;
        me.pushCanvasSize();
      });
    },
  }
</script>

<style lang="scss" rel="stylesheet/scss">
  .canvas-resizer {
    position: absolute;
    bottom: -31px;
    left: 50%;
    transform: translateX(-50%);
    user-select: none;
    text-align: center;
    .resizer {
      margin: auto;
      width: 200px;
      height: 30px;
      line-height: 30px;
      background: #fff;
      cursor: ns-resize;
      color: #acb5b3;
      font-size: 12px;
      .text {
        margin: 0 20px;
      }
      &:hover, &.active {
        color: #02c2cd;

      }
      .icon {
        height: 20px;
        width: 20px;
        line-height: 20px;
        cursor: pointer;
        display: inline-block;
        &:hover {
          color: #02c2cd;
        }
      }
    }
  }
</style>



