<template>
  <div
    class="uploadBtn"
  >
    <div class="add">
      <i class="fa"
         :class="{'fa-plus':!uploading,'fa-spinner':uploading,spinning:uploading}"></i>
      <div class="add-title">上传图片</div>
    </div>
    <input
      ref="uploadBtn"
      v-show="!uploading"
      type="file"
      class="upload-input"
      id="uploadBtn"
      @click="onClick"
      @change="selectFiles"
      multiple="multiple"
      accept="image/gif,image/jpg,image/jpeg,image/png">
  </div>
</template>

<!--TODO input v-if checkLogin-->
<!--TODO input  v-show uploading-->

<script>
  import { compressImg, genImageFileName } from 'helpers';
  import { uploadLibImg } from 'api';
  import { mapGetters } from 'vuex';
  import * as T from '../../store/types';

  // 完成后要清空 input 么？不清空下一次如果提交一样的文件就没反应
  export default {
    props: ['onOneDone', 'onClick'],
    computed: {
      ...mapGetters({
        uploading: T.IS_PIC_LIB_UPLOADING,
      })
    },
    methods: {
      selectFiles (e) {
        this.$store.commit(T.SET_IS_PIC_LIB_UPLOADING, true);
        const files = e.target.files;
        const promiseArr = [];
        const me = this;
        for (let i = 0, len = files.length; i < len; i++) {
          let file = files[i];
          const promise = new Promise((resolve) => {
            const md5FileName = genImageFileName(file);
            compressImg(file).then((base64DataURI)=> {
              uploadLibImg({
                name: md5FileName,
                base64: base64DataURI.split(',')[1],
                done (url) {
                  me.onOneDone && me.onOneDone(url);
                  resolve();
                }
              });
            });
          });
          promiseArr.push(promise);
        }
        Promise.all(promiseArr).then(()=> {
          this.$store.commit(T.SET_IS_PIC_LIB_UPLOADING, false);
        })
      },
    }
  };
</script>

<style scoped lang="scss" rel="stylesheet/scss">
  .upload-input {
    position: absolute;
    width: 100%;
    height: 100%;
    opacity: 0;
    display: block;
    top: 0;
    left: 0;
    cursor: pointer;
  }
</style>



