import * as T from './types';
import { numToPx } from 'helpers';

const notNull = val => (val !== null) && (val !== undefined);

export default {
  [T.IS_PREVIEW_VISIBLE](state) {
    return state.previewVisible;
  },

  [T.GET_CTRL_PANEL_TYPE](state) {
    return state.controlPannelType;
  },

  [T.GET_FOLK_ID](state) {
    return state.folkId;
  },
  [T.GET_OWN_ID](state) {
    return state.ownId;
  },

  [T.GET_META](state) {
    return state.meta;
  },

  [T.CONVERT_CANVAS_DATA](state) {
    return {
      canvasSize: state.canvasSize,
      content: state.elemIdArr.map(id => state.elems[id]),
      background: state.background,
    };
  },

  [T.IS_GRID_LINE_VISIBLE](state) {
    return state.isGridLineVisible;
  },

  [T.IS_PIC_LIB_UPLOADING](state) {
    return state.picLibUploading;
  },

  [T.IS_ELEM_MOVING](state) {
    return state.isElemMoving;
  },

  [T.GET_SEL_ELEM](state) {
    if (notNull(state.selectedElemId)) {
      return state.elems[state.selectedElemId];
    }
    return null;
  },

  [T.GET_ATTRACT_MOVING_INFO](state) {
    return state.attractMovingInfo;
  },

  [T.GET_EDITING_ELEMENT_ID](state) {
    return state.editingElementId;
  },

  [T.GET_SEL_ELEM_ID](state) {
    return state.selectedElemId;
  },

  [T.GET_ELEMS](state) {
    console.log(`${T.GET_ELEMS}`, state.elems);
    return state.elems;
  },

  [T.GET_ELEM_ID_ARR](state) {
    console.log(`${T.GET_ELEM_ID_ARR}`);
    return state.elemIdArr;
  },

  [T.GET_PRE_CHOOSE_OBJ](state) {
    console.log(`${T.GET_PRE_CHOOSE_OBJ}`);
    return state.preChooseObj;
  },

  [T.GET_CANVAS_SIZE](state) {
    console.log(`${T.GET_CANVAS_SIZE}`);
    return state.canvasSize;
  },

  [T.GET_SCALED_CANVAS_SIZE_PX](state) {
    console.log(`${T.GET_SCALED_CANVAS_SIZE_PX}`);
    const scale = state.scale;
    const canvasSize = state.canvasSize;
    return {
      height: numToPx(canvasSize.height * scale),
      width: numToPx(canvasSize.width * scale),
    };
  },

  [T.GET_SCALE_VAL](state) {
    console.log(`${T.GET_SCALE_VAL}`);
    return state.scale;
  },
};



