import _ from 'lodash';

const model = {
  style: {
    top: 780,
    left: 68,
    height: 0,//85,
    width: 0,//510,
    'font-size': 60,
    opacity: 1,
    rotate: '0',
    'border-radius': 3,
    'box-shadow': 0,
    color: '#FFFFFF',
    'background-color': '#292a29',
    'background-image': '',
    'background-size': '100',
  },
  type: 'linkButton',
  elementAnimations: {
    animation_in: {
      show: 'fadeIn',
      speed: 1000,
      delay: 600,
    },
  },
  url: '',
  // text: '自定义',
  // url: 'xx'
  // display: 'img' / 'text'
  // img: 'xx.imgurl.xxs.png'
};

const list = [{
  tel: '88886666',
  linkType: 'tel', // 'tel' 'link'
  display: 'img', // {display:'img',img:xx}  {display:'text',text:xx}
  img: 'http://odoiwipoe.bkt.clouddn.com/contact.png',
}].map(item => _.merge({}, model, item));


export default list;



