import * as T from './types';
import {
  createMeta,
  savePageByMetaId,
  queryMeta,
  queryPageByMeta,
  updateMeta,
  folkMeta,
} from 'api';
import { getUrlParam } from 'helpers';
import DEFAULT_PAGE_DATA from '../constant/default-page-data';

export default {
  [T.INIT_DATA_FROM_URL](store) {
    const ownId = getUrlParam('id'); // 打开自己作品
    const folkId = getUrlParam('fid'); // 打开别人作品
    const id = ownId || folkId;
    return new Promise((resolve, reject) => {
      if (id) {
        console.log('查询 id', id);
        queryMeta(id).then((meta) => {
          const metaId = meta.objectId;
          queryPageByMeta(metaId).then((page) => {
            store.commit(T.SET_META, meta);
            if (ownId) {
              store.commit(T.SET_OWN_ID, ownId);
            } else if (folkId) {
              store.commit(T.SET_FOLK_ID, folkId);
            }
            store.commit(T.INIT_CANVAS, page.data);
            resolve();
          });
        });
      } else {
        // 新建作品
        store.commit(T.INIT_CANVAS, DEFAULT_PAGE_DATA);
        resolve();
      }
    });
  },

  [T.SYNC_SAVE_CANVAS](store) {
    return new Promise((resolve, reject) => {
      store.dispatch(T.SYNC_CONFIRM_OWN_META)//
        .then((metaId) => {
          const canvas = store.getters[T.CONVERT_CANVAS_DATA];
          savePageByMetaId(metaId, canvas).then(resolve, reject);
        });
    });
  },

  [T.SYNC_UPDATE_META](store, updatedMetaInfo) {
    const ownId = store.getters[T.GET_OWN_ID];
    console.log('>>>T.SYNC_UPDATE_META', ownId)
    return new Promise((resolve, reject) => {
      store.dispatch(T.SYNC_CONFIRM_OWN_META, updatedMetaInfo).then((metaId)=> {
        if (!ownId) {
          const canvas = store.getters[T.CONVERT_CANVAS_DATA];
          savePageByMetaId(metaId, canvas).then(resolve, reject);
        } else {
          resolve()
        }
      }, reject);
    });
  },

  [T.SYNC_CONFIRM_OWN_META](store, updatedMetaInfo) {
    console.log('>>>SYNC_CONFIRM_OWN_META');
    return new Promise((resolve, reject) => {
      const ownId = store.getters[T.GET_OWN_ID];
      const folkId = store.getters[T.GET_FOLK_ID];
      if (ownId) {
        console.log('exist own meta');
        if (updatedMetaInfo) {
          updateMeta(ownId, updatedMetaInfo).then((meta) => {
            store.commit(T.SET_META, meta);
            resolve(ownId);
          });
        } else {
          resolve(ownId);
        }
      } else {
        let metaPromise;
        if (folkId) {
          console.log('folk meta');
          metaPromise = folkMeta(folkId, updatedMetaInfo);
        } else {
          console.log('new meta');
          metaPromise = createMeta(updatedMetaInfo);
        }
        metaPromise.then((meta) => {
          store.commit(T.SET_OWN_ID, meta.objectId);
          store.commit(T.SET_META, meta);
          resolve(meta.objectId);
        }, reject);
      }
    });
  },


};




