<template>
  <div :style="canvaSize" class="canvas">
    <auto-pos-line v-if="isPosLineShow"/>
    <canvas-grid v-show="isGridLineVisible"/>
    <element-canvas/>
    <canvas-resizer/>
  </div>

</template>

<script>
  import { mapGetters } from 'vuex';
  import * as T from '../store/types';
  import ElementCanvas from './ElementCanvas';
  import AutoPosLine from './AutoPosLine';
  import CanvasResizer from './CanvasResizer';
  import CanvasGrid from './CanvasGrid';

  export default {
    props: {
      canvasReady: {
//        required: true,
//        type: String,
      },
    },
    components: {
      AutoPosLine,
      ElementCanvas,
      CanvasResizer,
      CanvasGrid,
    },

    computed: {
      ...mapGetters({
        canvaSize: T.GET_SCALED_CANVAS_SIZE_PX,
        selectedElementId: T.GET_SEL_ELEM_ID,
        editingTextElementId: T.GET_EDITING_ELEMENT_ID,
        selectedElement: T.GET_SEL_ELEM,
        isGridLineVisible: T.IS_GRID_LINE_VISIBLE,
      }),
      isPosLineShow: function () {
        return this.selectedElementId &&
          !this.editingTextElementId &&
          this.selectedElement.type !== "group";
      },
    },
  };
</script>

<style lang="scss" rel="stylesheet/scss">
  .canvas {
    position: relative;
    margin: auto;
    background: #fff;
    box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.2);
  }

</style>



