<template>
  <div class="ctrl-panel" _v-5d23fe8d="">
    <keep-alive>
      <component :is="compClass">
      </component>
    </keep-alive>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex';
  import * as T from '../../store/types';
  import MapPanel from './MapCtrlPanel.vue';
  import LinkButtonPanel from './LinkButtonCtrlPanel';
  import ImgPanel from './ImgCtrlPanel.vue';
  import TextPanel from './TextCtrlPanel.vue';

  const CLASS = {
    map: MapPanel,
    linkButton: LinkButtonPanel,
    img: ImgPanel,
    text: TextPanel,
  };

  export default {
    name: 'ctrl-panel',
    computed: {
      ...mapGetters({
        ctrlPanelType: T.GET_CTRL_PANEL_TYPE,
      }),
      compClass() {
        return CLASS[this.ctrlPanelType];
      }
    },
  };
</script>

<style lang="scss" rel="stylesheet/scss">
  .ctrl-panel {
    position: absolute;
    top: 50px;
    right: 0px;
    width: 300px;
    bottom: 0;
    z-index: 1;
    background-color: #e6e6e6;
    .pannel {
      color: #333;
      font-size: 14px;
      font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    }
    .panel-title {
      line-height: 44px;
      text-align: center;
      &.border-bottom  {
        border-bottom: 1px solid #d6d6d6;
      }
    }
    .panel-content {
      padding: 10px 20px 20px 20px;
      .el-form-item {
        margin-bottom: 8px;
        .el-input__inner{
          border: none;
        }
      }
    }
  }

</style>



