<template>
  <div class="grid-container">
    <div class="grid-row">
      <div class="row-line" v-for="row in rowNum"></div>
    </div>
    <div class="grid-col">
      <div class="col-line" v-for="col in colNum"></div>
    </div>
  </div>
</template>

<script>
  import * as T from '../store/types';
  import { mapGetters } from 'vuex';

  export default {
    computed: {
      ...mapGetters({
        canvasSizePx: T.GET_SCALED_CANVAS_SIZE_PX,
      }),
      rowNum() {
        return Math.ceil(parseFloat(this.canvasSizePx.height) / 10)
      },
      colNum() {
        return Math.ceil(parseFloat(this.canvasSizePx.width) / 10)
      }
    }
  };
</script>

<style lang="scss" rel="stylesheet/scss">

  .grid-container {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 2;
    -webkit-user-drag: none;
    pointer-events: none;
    .grid-row,
    .grid-col {
      position: absolute;
      width: 100%;
      height: 100%;
    }
    .row-line {
      width: 100%;
      border-top: 1px dotted #d6d6d6;
      margin-bottom: 9px;
      &:last-child{
        margin-bottom: 0px;
      }
    }
    .col-line {
      float: left;
      border-left: 1px dotted #d6d6d6;
      height: 100%;
      margin-right: 9px;
      &:last-child{
        margin-right: 0px;
      }
    }
  }
</style>



