import _ from 'lodash';

const TEXT_MODEL_LIB1 = {
  type: 'text',
  href: null,
  content: '大标题',
  style: {
    height: 143,
    fontSize: 110,
    fontTag: '迷你简菱心',
  },
};

const TEXT_MODEL_LIB2 = {
  type: 'text',
  href: null,
  content: '标题',
  style: {
    height: 96,
    fontSize: 80,
    fontTag: '汉真广标',
  },
};
const TEXT_MODEL_LIB3 = {
  type: 'text',
  href: null,
  content: '副标题',
  style: {
    height: 72,
    fontSize: 60,
    fontTag: '思源黑体Medium',
  },
};
const TEXT_MODEL_LIB4 = {
  type: 'text',
  href: null,
  content: '小标题',
  style: {
    height: 80,
    fontSize: 40,
    fontTag: '思源黑体Regular',
  },
};

const TEXT_MODEL_LIB5 = {
  type: 'text',
  href: null,
  content: '正文内容',
  style: {
    height: 54,
    fontSize: 36,
    fontTag: '思源黑体Light',
  },
};

const TEXT_MODEL_LIB6 = {
  type: 'text',
  href: null,
  content: '小小正文内容',
  style: {
    height: 36,
    fontSize: 10,
    fontTag: '思源黑体Light',
  },
};
const list = [TEXT_MODEL_LIB1, TEXT_MODEL_LIB2,
  TEXT_MODEL_LIB3, TEXT_MODEL_LIB4, TEXT_MODEL_LIB5,
  /*, TEXT_MODEL_LIB6*/
].map((item) => {
  const ret = _.merge({
    type: 'text',
    href: null,
    opacity: 1,
    style: {
      width: 640,
      top: 0,
      left: 0,
      color: 'white',
      textAlign: 'center',
    },
  }, item);
  ret.style['line-height'] = ret.style.height / ret.style.fontSize;
  return ret;
});


export default {
  list,
};



