<template>
  <div class="link-sub-lib res-sub-lib">
    <!--<div class="sub-lib-title-wrap">-->
    <!--<div class="sub-lib-title border-bottom">跳转链接</div>-->
    <!--</div>-->
    <div class="sub-lib-content">
      <div v-for="col in cols" class="col">
        <enable-add
          class="link-item list-item"
          v-for="(item,index) in list"
          v-if="index % cols.length == col"
          :model="convertModel(item)"
          :sizepromise="sizePromise(item)"
          :click-cb="onClickAdd">
          <!--<div v-if="item.default" v-text="item.content" class="defaultWrap"></div>-->
          <div v-if="item.display==='img'" class="img-wrap">
            <img :src="item.img">
          </div>
        </enable-add>
      </div>
    </div>
  </div>
</template>

<script>
  import Base from '../Base';
  import MODEL_LIST from '../../constant/link-button-model-list';
  import { mapGetters } from 'vuex';
  import * as T from '../../store/types';
  import EnableAdd from './EnableAdd';
  import { getImageNaturalSize } from 'helpers';
  import _ from 'lodash';

  const DEFAULT_BTN_H = 85;
  const DEFAULT_BTN_W = 510;

  export default {
    mixins: [Base],
    components: {
      EnableAdd,
    },
    data () {
      return {
        cols: [0, 1],
        list: MODEL_LIST,
      }
    },
    computed: {
      ...mapGetters({
        canvasSize: T.GET_CANVAS_SIZE,
      }),
    },
    methods: {
      convertModel: function (model) {
        return _.merge({}, model);
      },
      sizePromise: function (model) {
        var me = this;
        return new Promise(function (resolve, reject) {
          if (model.display === 'img') {
            getImageNaturalSize(model.img).then((size) => {
              // TODO constrain as param for getImageNaturalSize
              size.width = Math.min(me.canvasSize.width, size.width);
              resolve(size);
            });
          } else {
            resolve({
              width: DEFAULT_BTN_W,
              height: DEFAULT_BTN_H,
            });
          }
        })
      },
      onClickAdd: function ({ size, model }) {
        const viewCenter = this.getViewCenter();
        const newModel = _.merge({}, model, {
          style: {
            width: size.width,
            height: size.height,
            left: parseInt(viewCenter.left - size.width / 2, 10),
            top: parseInt(viewCenter.top - size.height / 2, 10),
          },
        });
        this.$store.commit(T.ADD_ELEMENT, {
          model: newModel,
          addingWay: 'click',
          autoSelect: true,
        });
      },
    }

  };
</script>

<style lang="scss" rel="stylesheet/scss">

</style>



