<template>
  <div :style="renderStyle" class="img-render"> </div>
</template>

<script>
  import Base from '../Base';

  export default {
    name: 'img-render',
    props: ["element", "scaleFunc"],
    mixins: [ Base ],
    computed: {
      renderStyle: function () {
        const { style, scaleWidth, inLeft, inTop, picId } = this.element;
        const url = picId;//getImgUrl(elem.picId); // TODO
        const scaleFunc = this.scaleFunc;
        const width = style.width;
        const height = style.height;
        const backgroundSize = scaleWidth ? scaleFunc(scaleWidth) : "cover";
        return {
          width: scaleFunc(width),
          height: scaleFunc(height),
          opacity: style.opacity,
          backgroundImage: "url(" + url + ")",
          backgroundSize: backgroundSize,
          backgroundPosition: scaleFunc(-inLeft) + " " + scaleFunc(-inTop),
          borderRadius: scaleFunc(style["border-radius"] / 200 * (height > width ? width : height)),
          boxShadow: this.shadow(scaleFunc(style["box-shadow"])),
          transform: this.deg(style.rotate)
        }
      },
    }
  };
</script>

<style lang="scss" rel="stylesheet/scss">
</style>



