<template>
  <div class="text-lib">
    <enable-add
      v-for="textModel in list"
      class="text-item"
      :model="convertModel(textModel)"
      :sizepromise="sizePromise(textModel)"
      :click-cb="clickAdd"
    >
      <text-render
        :element="textModel"
        :scale-func="libScale"
      ></text-render>
    </enable-add>
    <div class="line"></div>
  </div>
</template>
