<script>
  import EnableAdd from './EnableAdd';
  import TextRender from '../element/TextRender';
  import { list } from '../../constant/text-model-list'
  import _ from 'lodash';
  import Base from '../Base';
  import * as T from '../../store/types';


  export default {
    mixins: [Base],
    components: {
      EnableAdd,
      TextRender,
    },
    data () {
      return {
        list
      }
    },
    methods: {
      libScale (fontSize) {
        return parseFloat(.5 * fontSize * 300 / 320).toFixed(3) + "px"
      },
      sizePromise(model) {
        return new Promise((resolve)=> {
          resolve({
            width: model.style.width,
            height: model.style.height
          });
        });
      },
      convertModel (model) {
        // TODO 如果 projectData.background.backgroundColor 是白色，则字体变黑色
        let modelForCanvas = _.merge({}, model);
        modelForCanvas.style.color = "#000";
        return modelForCanvas;
      },
      clickAdd: function (obj) {
        console.log('>>> <enable-add> clickAdd', obj);
        var me = this,
          model = obj.model,
          size = obj.size,
          viewCenter = this.getViewCenter(),
          newModel = _.cloneDeep(model);

        _.extend(newModel.style, {
          left: parseInt(viewCenter.left - size.width / 2),
          top: parseInt(viewCenter.top - size.height / 2),
          width: size.width,
          height: size.height
        });
//        this.preChoose(model.id, "text-item");
        this.$store.commit(T.ADD_ELEMENT, {
          model: newModel,
          addingWay: 'click',
          autoSelect: true,
        });
      },
    }

  }
</script>

<style scoped lang="scss" rel="stylesheet/scss">
  .text-lib {
    overflow-y: hidden;
    outline: none;
    position: absolute;
    top: 0px;
    bottom: 0;
    left: 0;
    right: 0;

    .text-item:hover {
      border: 2px solid #02c2cd;
    }

    .line {
      border-top: solid 1px #052b29;
      width: 96%;
      margin-left: 2%;
      margin-top: 5px;
    }
  }
</style>



