<template>
  <div class="map-sub-lib res-sub-lib">
    <!--
    <div class="sub-lib-title-wrap">
      <div class="sub-lib-title border-bottom">地图</div>
    </div>
        -->
    <div class="sub-lib-content">
      <div class="col full" v-for="col in cols">
        <enable-add class="map-item list-item"
                  v-for="(model,index) in list"
                  v-if="index % cols.length == col"
                  :model="convertModel(model)"
                  :sizepromise="sizePromise(model)"
                  :click-cb="onClickAdd"
        >
          <div class="img-wrap">
            <img :src="model.img">
          </div>
        </enable-add>
      </div>
    </div>
  </div>
</template>

<script>
  import Base from '../Base';
  import Map_MODEL_LIST from '../../constant/map-model-list';
  import EnableAdd from './EnableAdd';
  import _ from 'lodash';
  import * as T from '../../store/types';

  export default {
    mixins: [Base],
    components: {
      EnableAdd,
    },
    data: function () {
      return {
        cols: [0],
        list: Map_MODEL_LIST,
      };
    },
    methods: {
      convertModel: function (model) {
        return _.merge({}, model)
      },
      sizePromise: function (model) {
        return new Promise((resolve, reject) => {
          resolve({
            width: model.style.width,
            height: model.style.height,
          });
        });
      },
      onClickAdd: function ({ model, size }) {
        // TODO to BASE_LIB
        const viewCenter = this.getViewCenter();
        const newModel = _.merge({}, model, {
          style: {
            width: size.width,
            height: size.height,
            left: parseInt(viewCenter.left - size.width / 2, 10),
            top: parseInt(viewCenter.top - size.height / 2, 10),
          },
        });
        this.$store.commit(T.ADD_ELEMENT, {
          model: newModel,
          addingWay: 'click',
          autoSelect: true,
        });
      }
    },
  };
</script>

<style lang="scss" rel="stylesheet/scss">

</style>



