<template>
  <div class="header">
    <div class="backBtn">
      <span class="icon-arrow-css fa fa-arrow-left"></span>
      <a href="/pcenter/index.html">所有作品</a>
    </div>
    <meta-setting/>
    <div class="savePreviewBtnArea">
      <save-btn ref="saveBtn"/>
      <div @click="toPreview" class="preview btn">
        <span class="icon-eye-css fa fa-eye"> </span>
        <span>预览</span>
      </div>
    </div>
  </div>
</template>
