<script>
  import { mapGetters } from 'vuex';
  import * as T from '../store/types';
  import MetaSetting from './MetaSetting';

  import SaveBtn from './SaveBtn';
  export default {
    computed: {},
    components: {
      SaveBtn,
      MetaSetting,
    },
    created(){
    },
    methods: {
      toPreview() {
        this.$refs.saveBtn.save().then(()=> {
          this.$store.commit(T.SET_PREVIEW_VISIBLE, true);
        });
      }
    }
  };
</script>

<style lang="scss" rel="stylesheet/scss">
  .header {
    z-index: 1000;
    position: relative;
    top: 0;
    height: 50px;
    left: 0;
    right: 0;
    user-select: none;
    background-image: linear-gradient(90deg, #00c4cd 0%, #6458aa 100%);
    font-size: 16px;
    .backBtn {
      color: #fff;
      position: absolute;
      left: 20px;
      height: 50px;
      line-height: 50px;
      cursor: pointer;
      .icon-arrow-css {
        margin-right: 10px;
      }
    }
    .savePreviewBtnArea {
      position: absolute;
      width: 240px;
      right: 0;
      color: #fff;
      line-height: 50px;
      font-size: 14px;
      .btn[class^='icon'] {
        margin-right: 8px;
        position: relative;
        top: 3px;
        font-size: 20px;
      }
      .btn.save {
        width: 80px;
        background: rgba(5, 43, 41, 0.4);
        .icon-save-css {
          top: 2px;
          font-size: 16px;
        }
        .icon-ok-css {
          font-size: 20px;
        }
        .icon-loading-css {
          width: 22px;
          font-size: 20px;
          display: inline-block;
          transform-origin: center;
          animation: rotate 0.8s linear infinite;
        }
      }
      .btn.preview {
        width: 100px;
        background: #02c2cd;
        .icon-eye-css {
          margin-right: 5px;
          font-size: 20px;
          position: relative;
          top: 3px;
        }
      }
    }
  }
</style>



