/*
添加完元素后默认选中
 */
import * as T from '../types';

const plugin = (store) => {
  store.subscribe((mutation) => {
    if (mutation.type === T.ADD_ELEMENT) {
      const payload = mutation.payload;
      const model = payload.model;
      // selectedElementId: t.json.elementId,
      //   controlPannel: {
      //   type: n ? n + "-pannel" : "bg-pannel"
      // }
      store.commit(T.SEL_ELEM, model);
    }
  });
};
export default plugin;




