<template>
  <div class="res-pannel">
    <div class="res-pannel-nav nav">
      <div
        v-for="(nav, index) in navs"
        class="nav-btn" :class="{ active: isActive(index) }"
        @click="switchActive(index)"
      >
        <i class="fa" :class="`fa-${nav.icon}`"></i>
        <span>{{nav.label}}</span>
      </div>
    </div>
    <div class="nav-lib">
      <keep-alive>
        <component :is="getActiveView()">
          <!-- inactive components will be cached! -->
        </component>
      </keep-alive>
    </div>
  </div>
</template>

<script>

  import TextView from './ResTextLib'
  import PicView from './ResPicLib'
  import InteractView from './ResInteractLib';

  export default {
    data: function () {
      return {
        activeIndex: 1,
        navs: [
          { label: '文本', icon: 'text-width', cls: 'text', view: TextView },
          { label: '素材', icon: 'picture-o', cls: 'img', view: PicView },
          { label: '互动', icon: 'gamepad', cls: 'interaction', view: InteractView },
        ]
      }
    },
    methods: {
      isActive (index) {
        return this.activeIndex === index;
      },
      switchActive (index) {
        console.log('switchIndex')
        this.activeIndex = index;
      },
      getActiveView () {
        return this.navs[this.activeIndex].view;
      }
    }
  }
</script>


<style>
  .res-pannel {
    position: absolute;
    top: 50px;
    left: 0px;
    width: 360px;
    bottom: 0;
    background-color: #373f42;
  }
</style>

<style lang="scss" rel="stylesheet/scss">
  .res-pannel {
    .res-pannel-nav {
      user-select: none;
      font-size: 12px;
      position: absolute;
      left: 0;
      top: 0;
      width: 60px;
      bottom: 0;
      background: #052b29;

      .nav-btn {
        height: 60px;
        width: 60px;
        text-align: center;
        line-height: 60px;
        position: relative;
        color: #fff;
        cursor: pointer;

        &.active {
          background: #373f42;
          color: #02c2cd;
        }

        i {
          position: absolute;
          font-size: 17px;
          top: 16px;
          left: 50%;
          transform: translateX(-50%);
        }

        span {
          position: absolute;
          top: 15px;
          left: 50%;
          transform: translateX(-50%);
        }
      }

    }
    .nav-lib {
      position: absolute;
      left: 60px;
      bottom: 0;
      top: 0;
      right: 0;
    }
  }
</style>





