import { rotateElemLayout } from '../../../../helpers/style';

export default {
  computed: {
    // TODO 这个来自 mixin-position-scale.js 看上去里面只有一个需要，就单独拿过来
    renderPosition() {
      if (!this.element) return {};
      return rotateElemLayout(this.element.style, this.scalePx);
    },
  },
};



