<template>
  <div class="link-button-element">
    <wrapper
      :element="element"
      :scale-func="scalePx"
      :selected="selectedElementId === elementId"
      :index="index"
      :num-of-elements="numOfElements"
    >
    <link-button-render
      class="link-button-render"
      :style="renderPosition"
      :element="element"
      :scale-func="scalePx"
    >
    </link-button-render>
    <operate
      :element="element"
      :element-id="elementId"
      :style="renderPosition"
      :scale-func="scalePx"
      :selected="selected"
    :show="selected"
    v-show="operatorVisible"
    >
    </operate>
    </wrapper>
  </div>
</template>

<script>
  import Base from '../Base';
  import BaseElement from './BaseElement';
  import * as T from '../../store/types';
  import { mapGetters } from 'vuex';
  import Wrapper from './ElementWrapper';
  import LinkButtonRender from './LinkButtonRender';
  import Operate from '../Operate';

  export default {
    name: 'link-button-element',
    mixins: [Base, BaseElement],
    props: ['elementId', 'element', 'numOfElements', 'index'],
    components: {
      Wrapper,
      LinkButtonRender,
      Operate,
    },
    computed: {
      ...mapGetters({
        selectedElementId: T.GET_SEL_ELEM_ID,
      }),
      // TODO to baseelement
      selected () {
        return this.selectedElementId === this.elementId;
      },
      croppingElementId () {
        // map state getter
        return null;
      },
      operatorVisible () {
        return true;
//        return this.croppingElementId !== this.elementId && !this.beingGroupSelect
      }
    },
  };
</script>

<style lang="scss" rel="stylesheet/scss">
</style>



