<template>
  <div class="element-canvas">
      <comopnent
        v-for="(elementId,index) in elemIdArr"
        :key="elementId"
        :is="elementType(elementId)"
        :element="element(elementId)"
        :element-id="elementId"
        :index="index"
        :num-of-elements="elemIdArr.length"
      >
      </comopnent>
  </div>
</template>

<script>
  import * as T from '../store/types';
  import TextElement from './element/TextElement';
  import ImgElement from './element/ImgElement';
  import LinkButtonElement from './element/LinkButtonElement';
  import MapElement from './element/MapElement';
  import { mapGetters } from 'vuex';

  export default {
    components: {
      TextElement,
      ImgElement,
      LinkButtonElement,
      MapElement,
    },
    computed: {
      ...mapGetters({
        elemIdArr: T.GET_ELEM_ID_ARR,
        elems: T.GET_ELEMS,
      }),
    },
    methods: {
      elementType (id) {
        return this.elems[id].type + "-element";
      },
      element (id) {
        return this.elems[id];
      },
    },
  };
</script>

<style lang="scss" rel="stylesheet/scss">
  .element-canvas {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 10;
  }
</style>



