<template>
  <div :class="{ hover: !selected }" @mousedown="selectElement" class="operate">
    <div v-show="show" @dblclick="callDbclickCb">
      <!--
      <div v-if="showRotate" class="rotate-btn">
        <span class="icon-xuanzhuang-css dyeditor-icon-xuanzhuan">
        </span>
      </div>
      -->
      <div class="border-line">
      </div>
      <div class="border-line dashed">
      </div>
      <scale-dot v-for="(item, index) in position"
                 v-if="showDot(item)"
                 :item="item"
                 :rotate="rotate"
                 :index="index"
                 :start-scale="startScale"
                 :scaling="scaling"
                 :end-scale="endScale"
      >
      </scale-dot>
    </div>
  </div>
</template>

<script>
  import * as T from '../store/types';
  import * as C from 'constant';
  import Moving from './OperateMoving';
  import ScaleDot from './OperateScaleDot';
  import Scaling from './OperateScaling';

  export default {
    name: "operate",
    props: ["element", "selected", "scaleFunc", "show", "dbclickCb", "onScaling"/*, "groupElement"*/],
    mixins: [ Moving, Scaling],
    components: {
      ScaleDot,
    },
    data: function () {
      return {
        start: {},
        readyToRotate: false,
        position: ["dot-nw", "dot-n", "dot-ne", "dot-e", "dot-se", "dot-s", "dot-sw", "dot-w"],
        rotateObj: void 0,
        vectorTop: {
          x: 0,
          y: 1,
        },
      };
    },
    computed: {
      eleData() {
        return this.element;
      },
      elementId() {
        return this.eleData.id;
      },
      rotate() {
        return this.eleData.style.rotate || 0;
      },
//      showRotate: function() {
//        return -1 === _.indexOf(this.eleData.type)
//      }
    },
    methods: {
      callDbclickCb(e) {
        this.dbclickCb && this.dbclickCb(e);
      },
      selectElement() {
        const eleData = this.eleData;
        const { type, voteType } = eleData;
        if ((type === 'img' || type === 'vote') && 3 != voteType) {
          // TODO
//          this.actions.selectResNav("img");
        }
        if (!this.selected) {
          this.$nextTick(() => {
            this.$store.commit(T.SEL_ELEM, eleData);
          });
        }
      },
      showDot(position) {
        const type = this.eleData.type;
        const d = position.replace("dot-", "");

        if (['relay', 'vote'].indexOf(type) > -1) {
          return false;
        }

        if (type === 'linkButton') {
          return this.eleData.isImg ? ['ne', 'nw', 'se', 'sw'].indexOf(d) !== -1 : true;
        } else if (['interactButton', 'countDown', 'video'].indexOf(type) > -1) {
          return ['ne', 'nw', 'se', 'sw'].indexOf(d) !== -1;
        } else if (type === 'form') {
          return d === 'e' || d === 'w';
        } else if (type === 'text') {
          return (d === 'e' || d === 'w') &&
            this.eleData.style['writing-mode'] !== 'vertical-lr';
        }
        return true;
      },
    }
  }

</script>

<style lang="scss" rel="stylesheet/scss">
  .operate {
    position: absolute;
    top: 50%;
    left: 50%;
    cursor: pointer;
    z-index: 99;
    -webkit-transform-origin: center center;
    transform-origin: center center;

    &.hover {
      &:hover {
        outline: 1px solid rgba(0, 0, 0, 0.5);
      }
    }

    .rotate-btn {
      position: absolute;
      top: -30px;
      left: 50%;
      width: 22px;
      height: 22px;
      margin-left: -11px;
      cursor: pointer;
      border-radius: 50%;
      background-color: #000;
    }

    .rotate-btn .icon-xuanzhuang-css {
      position: absolute;
      left: 50%;
      font-size: 12px;
      top: 50%;
      -webkit-transform: translate3d(-50%, -50%, 0);
      transform: translate3d(-50%, -50%, 0);
      color: #fff;
    }

    .border-line {
      position: absolute;
      box-sizing: content-box;
      width: 100%;
      height: 100%;
      margin: -1px 0 0 -1px;
      border: 1px solid #fff;
    }

    .border-line.dashed {
      border: 1px dashed #000;
    }

    .dot {
      position: absolute;
      background-color: #fff;
      width: 5px;
      height: 5px;
      border: 1px solid #4a4a4a;
    }

    .dot.dot-sw,
    .dot.dot-se,
    .dot.dot-nw,
    .dot.dot-ne {
      width: 9px;
      height: 9px;
      border-radius: 50%;
    }

    .dot-nw {
      top: -5px;
      left: -5px;
    }

    .dot-ne {
      top: -5px;
      right: -5px;
    }

    .dot-sw {
      bottom: -5px;
      left: -5px;
    }

    .dot-se {
      bottom: -5px;
      right: -5px;
    }

    .dot-n {
      left: 50%;
      top: -4px;
      margin-left: -4px;
    }

    .dot-e {
      top: 50%;
      right: -4px;
      margin-top: -4px;
    }

    .dot-s {
      bottom: -4px;
      left: 50%;
      margin-left: -4px;
    }

    .dot-w {
      top: 50%;
      left: -4px;
      margin-top: -4px;
    }

    .scale-nw {
      cursor: nw-resize;
    }

    .scale-n {
      cursor: n-resize;
    }

    .scale-ne {
      cursor: ne-resize;
    }

    .scale-e {
      cursor: e-resize;
    }

    .scale-se {
      cursor: se-resize;
    }

    .scale-s {
      cursor: s-resize;
    }

    .scale-w {
      cursor: w-resize;
    }

    .scale-sw {
      cursor: sw-resize;
    }

  }

</style>



