<template>
    <el-autocomplete
      class="address-autocomplete"
      custom-item="address-item-view"
      :value="keyword"
      :fetch-suggestions="fetchSuggestions"
      placeholder="请输入地址关键字"
      @select="handleSelect"
    ></el-autocomplete>
</template>

<script>
  import Vue from 'vue';
  import {
    Autocomplete
  } from 'element-ui'
  import { searchAddressByKeyword } from 'api';
  import _ from 'lodash';

  Vue.use(Autocomplete);

  Vue.component('address-item-view', {
    functional: true,
    render: function (h, ctx) {
      var item = ctx.props.item;
      return h('li', ctx.data, [
        h('div', { attrs: { class: 'name' } }, [item.value]),
        h('span', { attrs: { class: 'addr' } }, [item.address])
      ]);
    },
    props: {
      item: { type: Object, required: true }
    }
  });

  export default {
    props: ['onSelect', 'keyword'],
    data(){
      return {
        lastKw: '',
        value: '', // 输入框的内容
      };
    },
    methods: {
      fetchSuggestions: _.debounce(function (kw, cb) {
        kw = (kw || '').trim();
        if (!kw) {
          cb(null);
        } else {
          this.lastKw = kw;
          searchAddressByKeyword(kw).then((list) => {
            if (kw !== this.lastKw) {
              return;
            }
            /*{
             "id": "10621959455628347303",
             "title": "丽景城",
             "address": "广东省深圳市宝安区前进二路100",
             "type": 0,
             "location": { "lat": 22.58827, "lng": 113.88612 },
             "adcode": 440306,
             "province": "广东省",
             "city": "深圳市",
             "district": "宝安区"
             }*/
            cb(list.map((item) => {
              return {
                value: item.title,
                title: item.title,
                location: item.location,
                address: item.address,
              }
            }));
          });
        }
      }, 350),
      handleSelect(item) {
        this.onSelect && this.onSelect(item);
      },
    }
  };
</script>

<style lang="scss" rel="stylesheet/scss">

  .address-autocomplete .el-autocomplete__suggestions {
    li {
      line-height: normal;
      padding-top: 7px;
      padding-bottom: 7px;
      .name {
        text-overflow: ellipsis;
        overflow: hidden;
      }
      .addr {
        font-size: 12px;
        color: #b4b4b4;
      }
    }
  }
</style>



