import * as T from '../store/types';
import * as C from 'constant';

export default {
  data() {
    return {
      startMoveInfo: null, // 移动的元素位置信息对象
      lockLeftAttract: false,
      lockTopAttract: false,
    };
  },
  methods: {
    startMove(e) {
      console.log('>>>startMove');
      e.stopPropagation();
      const style = this.eleData.style;
      this.startMoveInfo = {
        left: style.left,
        top: style.top,
        startX: e.pageX,
        startY: e.pageY,
      };
      this.$store.commit(T.SET_IS_ELEM_MOVING, true);
    },
    moving(e) {
      console.log('>>moving');
      if (!this.startMoveInfo) {
        return;
      }
      let changed = false;
      const startMoveInfo = this.startMoveInfo; // 最初坐标信息
      const eleData = this.element;
      const r = parseFloat(this.scaleFunc(1));
      const left = parseInt(startMoveInfo.left + ((e.pageX - startMoveInfo.startX) / r), 10);
      const top = parseInt(startMoveInfo.top + ((e.pageY - startMoveInfo.startY) / r), 10);
      const attractInfo = this.$store.getters[T.GET_ATTRACT_MOVING_INFO];
      const elemLeft = eleData.style.left;
      const elemTop = eleData.style.top;
      let finalLeft = elemLeft;
      let finalTop = elemTop;
      // // console.log('moving ', attractInfo);
      if (attractInfo.left != null) {
        // console.log('>>>moving attractInfo.left not null')
        if (this.lockLeftAttract) {
          // console.log('>>is lock left')
          // 如果元素左边吸附了绿线的情况下，移动超过范围
          if (Math.abs(left - elemLeft) > C.ATTRACT_OFFSET) {
            // console.log('>>> free left lock')
            this.lockLeftAttract = false;
            finalLeft = left;
            changed = true;
          }
        } else { // 元素左边吸住
          // console.log('>>> abstract left')
          finalLeft = elemLeft + attractInfo.left;
          this.lockLeftAttract = true;
          changed = true;
        }
      } else {
        // console.log('>>>moving attractInfo.left null')
        finalLeft = left;
        this.lockLeftAttract = false;
        changed = true;
      }

      if (attractInfo.top != null) {
        if (this.lockTopAttract) {
          if (Math.abs(top - elemTop) > C.ATTRACT_OFFSET) {
            this.lockTopAttract = false;
            finalTop = top;
            changed = true;
          }
        } else {
          finalTop = elemTop + attractInfo.top;
          this.lockTopAttract = true;
          changed = true;
        }
      } else {
        finalTop = top;
        this.lockTopAttract = false;
        changed = true;
      }
      if (changed) {
        console.log('>>>T.UPDATE_ELEM_POS')
        this.$store.commit(T.UPDATE_ELEM_POS, {
          id: eleData.id,
          left: finalLeft,
          top: finalTop,
        });
      }
    },
    endMove() {
      if (this.startMoveInfo) {
        this.$store.commit(T.SET_IS_ELEM_MOVING, false);
        this.startMoveInfo = null;
        // TODO
//            this.informGroupToResize()
      }
    },
  },
  mounted() {
    // https://github.com/stephband/jquery.event.move
    $(this.$el)//
      .on('movestart', this.startMove)//
      .on('move', this.moving)//
      .on('moveend', this.endMove);
    // TODO off when beforeDestroy
  },
};



