<template>
  <img :src="imgSrc" :class="clsObj">
</template>

<script>

  import { mapGetters } from 'vuex';
  import * as T from '../../store/types';

  const UPLOADING_INDICATOR_IMG = "data:image/png;base64,"

  export default {
    props: ['src'],
    data () {
      return {
        loaded: false,
        tm: null,
      }
    },
    computed: {
      ...mapGetters({
        uploading: T.IS_PIC_LIB_UPLOADING
      }),
      imgSrc () {
        if (this.loaded) {
          return this.src;
        } else if (this.uploading) {
          return UPLOADING_INDICATOR_IMG;
        }
        return null;
      },
      clsObj () {
        return {
          spinning: !this.loaded && this.uploading
        };
      }

    },
    methods: {
      startLoadImg () {
        if (!this.src) {
          return;
        }
        this.loaded = false;
        var img = new Image;
        this.tm = img.tm = Date.now();
        img.onload = ()=> {
          if (this.tm === img.tm) {
            this.loaded = true;
          }
        };
        img.src = this.src;
      }
    },
    watch: {
      src (val, oldVal) {
        if ((val !== oldVal) && oldVal) {
//          console.log('watch change')
          this.startLoadImg()
        }
      }
    },
    created () {
      if (this.src) {
        this.startLoadImg();
      }
    },
  };
</script>


