<template>
  <div class="scale">
    <div v-show="dropdownShow" class="drop-down-list">
      <ul>
        <li v-for="(item,$index) of scaleList" @click="switchScale(item)">
          <span>{{displayScaleItemName(item)}}</span>
          <div v-show="selectedScale == $index" class="done-icon-css dyeditor-icon-done"></div>
        </li>
      </ul>
      <div class="triangle">
        <div class="top"></div>
        <div class="left"></div>
        <div class="bottom"></div>
        <div class="right"></div>
      </div>
    </div>
    <div @click="scaleDown" :class="{stop:minusDiabled}" class="min-wrap">
      <div></div>
    </div>
    <div @click.stop="toggleDropdown" class="drop-down-wrap">
      <span>{{percentText}}</span>
    </div>
    <div @click="scaleUp" :class="{stop:pluDisabled}" class="add-wrap">
      <div></div>
      <div></div>
    </div>
  </div>
</template>

<script>
  import { SCALE, INIT_CANVAS_HEIGHT, INIT_CANVAS_WIDTH } from 'constant';
  import * as T from '../store/types';
  import { mapGetters } from 'vuex';
  import _ from 'lodash';


  const SCALE_DROPDOWN_LIST = ['25', '50', '75', '100', '125', '150', '200', 'fill', 'fit'];
  const SCALE_BUS = [.125, .25, .375, .5, .625, .75, 1, 1.5, 2, 2.5];
  var SCALE_BUS_MAX = SCALE_BUS[SCALE_BUS.length - 1];

  //    return

  function backOne(arr, target) {
    for (var i = 0, len = arr.length; i < len; i++) {
      if (target < arr[i]) {
        return i;
      }
    }
    return null;
  }

  export default {
    data () {
      return {
        scaleList: SCALE_DROPDOWN_LIST,
        scaleBus: SCALE_BUS,
        selectedScale: null,
        dropdownShow: false,
        addStop: false,
        minStop: false
      }
    },
    computed: {
      ...mapGetters({
        scaleVal: T.GET_SCALE_VAL
      }),
      percentText () {
        return `${parseInt((this.scaleVal / SCALE) * 100, 10)}%`
      },
      pluDisabled () {
        return this.scaleVal >= this.scaleBus[this.scaleBus.length - 1];
      },
      minusDiabled () {
        return this.scaleVal <= this.scaleBus[0];
      },
    },
    created () {
      this.switchScale('fit');
    },
    methods: {
      // 下拉切换
      switchScale (t, omitCapture) {
        var newScale = void 0,
          scaleList = this.scaleList;
        this.selectedScale = scaleList.indexOf(t);
        if ("fill" == t) {
          var ctW = $(".canvas-container").width(),
            canvasW = $(".canvas").width(),
            s = ctW - 108,
            l = this.scaleVal;
          newScale = s / canvasW * l;
        } else if ("fit" == t) {
          newScale = this.useFitScale();
        }
        else {
          newScale = .5 * parseInt(t) / 100
        }
        this.updateScale(newScale);
        ``
      },
      updateScale (scale) {
        this.$store.commit(T.SET_SCALE_VAL, scale);
        this.rem(scale);
        this.refreshScroll();
      },
      rem: function (scale) {
        document.documentElement.style.fontSize = INIT_CANVAS_WIDTH * scale / 10 + "px"
      },
      refreshScroll: function () {
        this.$nextTick(function () {
          const myScroll = window.myScroll;
          const { x, y, maxScrollX, maxScrollY } = window.myScroll;
          myScroll.refresh();
          myScroll.scrollTo(
            maxScrollX === 0 ? maxScrollX * 0.5 : x,
            maxScrollY === 0 ? maxScrollY * 0.5 : y
          );
        })
      },
      scaleDown () {
        if (this.minusDiabled) {
          return;
        }
        const curScale = this.scaleVal;
        const frontOne = _.find(this.scaleBus.slice(0).reverse(), (v)=> {
          return v < curScale
        });
        if (frontOne) {
          this.updateScale(frontOne)
        }
      },
      scaleUp () {
        if (this.pluDisabled) {
          return;
        }
        const curScale = this.scaleVal;
        const backOne = _.find(this.scaleBus, (v)=> {
          return v > curScale
        });
        if (backOne) {
          this.updateScale(backOne)
        }
      },
      displayScaleItemName: function (e) {
        return "fill" == e ? "满屏" : "fit" == e ? "最佳" : e + "%"
      },
      toggleDropdown () {
        this.dropdownShow = !this.dropdownShow;
      },
      useFitScale () {
        var scale = (0.75 * window.innerHeight) / INIT_CANVAS_HEIGHT;
        return Math.min(scale, SCALE_BUS_MAX);
      },
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss">


  .scale {
    width: 120px;
    height: 36px;
    background-color: #373f42;
    position: absolute;
    bottom: 40px;
    right: 50px;
    border-radius: 18px;
    .drop-down-list {
      position: absolute;
      bottom: 50px;
      background-color: #373f42;
      text-align: left;
      width: 100%;
      color: #fff;
      box-sizing: border-box;
      ul {
        padding: 7px 0;
        margin: 0;
        li {
          list-style-type: none;
          font-size: 14px;
          padding: 7px 15px;
          cursor: pointer;
          &:hover {
            color: #02c2cd;
          }
          .done-icon-css {
            display: inline-block;
            float: right;
          }
        }
      }
      .triangle {
        position: absolute;
        width: 0;
        height: 0;
        border-top: 7px solid #373f42;
        border-left: 7px solid transparent;
        border-bottom: 7px solid transparent;
        border-right: 7px solid transparent;
        bottom: -13px;
        left: 50%;
        -webkit-transform: translateX(-50%);
        transform: translateX(-50%);
      }
    }
    .min-wrap {
      cursor: pointer;
      position: absolute;
      left: 0;
      height: 100%;
      width: 25%;
      &:hover div {
        background-color: #02c2cd;
      }
      &.stop div {
        background-color: #808080;
        cursor: not-allow;
      }
      div {
        background-color: #fff;
        height: 2px;
        width: 10px;
        position: absolute;
        top: 50%;
        right: 0;
        -webkit-transform: translateY(-50%);
        transform: translateY(-50%);
      }
    }
    .drop-down-wrap {
      cursor: pointer;
      font-size: 14px;
      color: #fff;
      position: absolute;
      top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
      transform: translate(-50%, -50%);
      &:hover {
        color: #02c2cd;
      }
    }
    .add-wrap {
      cursor: pointer;
      position: absolute;
      right: 0;
      height: 100%;
      width: 25%;
      &:hover div {
        background-color: #02c2cd;
      }
      &.stop div {
        background-color: #808080;
        cursor: not-allow;
      }
      div {
        background-color: #fff;
        &:first-child {
          height: 2px;
          width: 10px;
          position: absolute;
          top: 50%;
          left: 0;
          -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
        }
        &:last-child {
          height: 10px;
          width: 2px;
          position: absolute;
          top: 50%;
          left: 4px;
          -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
        }
      }
    }
  }

</style>



