<template>
  <div v-show="visible" class="text-element">
    <wrapper
      :element="element"
      :scale-func="scalePx"
      :selected="selected"
      :index="index"
      :num-of-elements="numOfElements"
    >
      <text-render
        ref="textRender"
        :style="renderPosition"
        :element="element"
        :scale-func="scalePx"
        :is-editor="true"
        :blur-cb="onTextInputEnd"
        :is-editing="isEditing"
        :keyup-cb="onTextKeyUp"
        :keydown-cb="onTextKeyDown"
      ></text-render>

      <operate
        :style="renderPosition"
        :element-id="elementId"
        :element="element"
        :selected="selected"
        :scale-func="scalePx"
        :dbclick-cb="onTextSelectEdit"
        :show="selected"
        v-show="!isEditing"
        :on-scaling="onScaling"
      ></operate>
    </wrapper>
  </div>
</template>

<script>
  import Wrapper from './ElementWrapper';
  import TextRender from './TextRender.vue';
  import Operate from '../Operate';
  import Base from '../Base';
  import { mapGetters } from 'vuex';
  import * as T from '../../store/types';
  import BaseElement from './BaseElement';
  import _ from 'lodash';

  export default {
    mixins: [Base, BaseElement],
    props: ["element", "elementId", "numOfElements", "index"],
    components: {
      Wrapper,
      TextRender,
      Operate,
    },
    data () {
      return {
        isEditing: false,
        isCtrlDown: false
      }
    },
    computed: {
      ...mapGetters({
        canvasSize: T.GET_CANVAS_SIZE,
        selectedElementId: T.GET_SEL_ELEM_ID,
        editingTextElementId: T.GET_EDITING_ELEMENT_ID,
      }),
      visible () {
        return true;
//        return !beingGroupSelect
      },
      selected () {
        return this.elementId === this.selectedElementId;
      },
      elementId () {
        return this.element.id;
      },
      fontScale: function () {
        const i = parseFloat(this.scalePx(this.element.style.fontSize));
        let fontScale = 1;
        if (12 > i) {
          fontScale = i / 12;
        }
        return fontScale;
      },
    },
    created () {

    },
    methods: {
      onTextInputEnd (e) {
        console.log('>>>onTextInputEnd');
        this.isEditing = false;
        this.restoreKeyboardPropagation();
        const fontScale = this.fontScale;
        const elem = _.merge({}, this.element);
        const editAreaDom = this.getEditAreaDom();
        const scale = this.scale(1);

        elem.content = e.target.innerHTML.replace(/\n/g, "<br/>")//
          .replace(/\<div\>(.*?)\<\/div\>/g, "<br/>$1");

        const newStyle = {};
        if (elem.style['writing-mode'] === 'vertical-lr') {
          elem.style.width = editAreaDom.scrollWidth / scale * fontScale
        } else {
          elem.style.height = editAreaDom.scrollHeight / scale * fontScale
        }
        this.$store.commit(T.UPDATE_ELEM, elem);
        if (this.elementId == this.editingTextElementId) {
          this.$store.commit(T.SET_EDITING_ELEMENT_ID, null);
        }
      },
      onTextKeyUp(e) {
        var keyCode = e.keyCode,
          canvasHeight = this.canvasSize.height,
          top = this.element.style.top,
          scale = this.scale(1),
          height = $(e.target).height(),
          bottomToCanvasBottom = height / scale + top - canvasHeight,
          winH = $(window).height();
        // TODO no window.
        window.myScroll.scrollTo(0, winH / 2 - height - top * scale);
        if (bottomToCanvasBottom > -100) {
          this.$store.commit(T.SET_CANVAS_SIZE, {
            height: canvasHeight + bottomToCanvasBottom + 100
          });
        }
        if (91 == keyCode || 17 == keyCode) {
          this.isCtrlDown = false;
        }
        this.$nextTick(() => {
          window.myScroll.refresh()
        });
      },
      onTextKeyDown(e) {
        var keyCode = e.keyCode;
        if (91 == keyCode || 17 == keyCode) {
          this.isCtrlDown = true;
        }
        if (this.isCtrlDown && 86 == keyCode) {
          window.myScroll.scrollTo(0, -9999999)
        }
      },
      onTextSelectEdit () {
        if (!this.isEditing) {
          this.isEditing = true;
          this.stopKeyboardPropagation();
          this.$store.commit(T.SET_EDITING_ELEMENT_ID, this.elementId);
        }
      },
      restoreKeyboardPropagation() {
        $(this.$el).on('.editing');
      },
      stopKeyboardPropagation() {
        const stopPropagation = (e) => e.stopPropagation();
        $(this.$el).on('keydown.editing', stopPropagation)//
          .on('keyup.editing', stopPropagation)//
          .on('keypress.editing', stopPropagation);
      },
      getEditAreaDom() {
        // TODO 这样好吗？传 cb 给 textRende 吧？
        return this.$refs.textRender.$refs.editArea;
      },
      onScaling() {
        const isVertical = "vertical-lr" == this.element.style["writing-mode"];
        const editArea = this.getEditAreaDom();
        const scale = this.scale(1);
        const fontScale = this.fontScale;
        let { width, height } = this.element.style;
        if (isVertical) {
          editArea.style.height = "100.001%";
          setTimeout(function () {
            editArea.style.height = "100%"
          });
          width = editArea.scrollWidth / scale * fontScale;
        } else {
          height = editArea.scrollHeight / scale * fontScale
        }
        this.$store.commit(T.UPDATE_ELEM_SIZE, {
          id: this.element.id,
          width,
          height,
        });
      },
    }
  };
</script>

<style lang="scss" rel="stylesheet/scss">
  .text-element {
    position: absolute;
  }
</style>



