<template>
  <div class="dot"
       :class="[direction(index), item]"
  >
  </div>
</template>

<script>
  export default {
    props: ['item', 'index', 'rotate', 'startScale', 'scaling', 'endScale'],
    methods: {
      direction (e) {
        const boundIndex = Math.floor((this.rotate + 22.5) / 45);
        const cls = ["scale-nw", "scale-n", "scale-ne", "scale-e",
          "scale-se", "scale-s", "scale-sw", "scale-w"];
        for (let i = 0; i < boundIndex; i += 1) {
          cls.push(cls.shift())
        }
        return cls[e]
      },
      getDirectionName() {
        const match = this.$el.className.match(/scale-([^\s$]+)/);
        return match ? match[1] : null;
      },
    },
    mounted() {
      const me = this;
      $(this.$el)//
        .on('movestart.operate', e => {
          return me.startScale(e, me.getDirectionName());
        })//
        .on('move.operate', e => {
          return me.scaling(e, me.getDirectionName());
        })//
        .on('moveend.operate', e => {
          return me.endScale(e, me.getDirectionName());
        });
    },
    beforeDestroy() {
      $(this.$el)//
        .off('movestart.operate')//
        .off('move.operate')//
        .off('moveend.operate');
    },
  }
</script>



