<template>
  <div class="line-container">
    <div :style="posStyle('left')" class="c-line"></div>
    <div :style="posStyle('right')" class="c-line"></div>
    <div :style="posStyle('xCenter')" class="c-line"></div>
    <div :style="posStyle('top')" class="r-line"></div>
    <div :style="posStyle('bottom')" class="r-line"></div>
    <div :style="posStyle('yCenter')" class="r-line"></div>
  </div>
</template>

<script>
  let counter = 1;
  import { mapGetters } from 'vuex';
  import * as T from '../store/types';
  import Base from './Base';
  import * as C from 'constant';
  import * as EvTypes from '../constant/events';
  import _ from 'lodash';

  let offsetToLine = {};

  const VERTICAL_LINE_PROPS = ['left', 'right', 'xCenter'];
  const HOR_LINE_PROPS = ['top', 'bottom', 'yCenter'];
  const LINE_PROPS = VERTICAL_LINE_PROPS.concat(HOR_LINE_PROPS);

  export default {
    name: "auto-pos-line",
    mixins: [Base],
    data: function () {
      window.line = this;
      return {
        viewBox: {}, // 画布上中下、左中右 六条基线
        elementsPos: { // 元素的边界几线，每增加一个元素， left:[] 和 top:[] 分别增加三项
          left: [],
          top: []
        },
      }
    },
    computed: {
      ...mapGetters({
        selectedElementId: T.GET_SEL_ELEM_ID,
        selElem: T.GET_SEL_ELEM,
        isElemMoving: T.IS_ELEM_MOVING,
        canvasSize: T.GET_CANVAS_SIZE,
        elems: T.GET_ELEMS,
      }),
      attractGap: function () {
        return C.ATTRACT_OFFSET * this.scale(1)
      },
      rotateStyle: function () {
        var roatedStyle = this.rotateBox(this.selElem.style);
        const { width, height, left, top } = roatedStyle;
        roatedStyle.right = left + width;
        roatedStyle.xCenter = left + parseInt(width / 2);
        roatedStyle.bottom = top + height;
        roatedStyle.yCenter = top + parseInt(height / 2);
        return roatedStyle;
      },
      // 当前选中元素相对于各条可吸附线的距离
      nearLinesInfo() {
        const attractGap = this.attractGap;
        const rotateStyle = this.rotateStyle;
        const ret = {};
        LINE_PROPS.forEach((posName) => {
          ret[posName] = null;
          const elemVal = rotateStyle[posName];
          const baseStyleProp = VERTICAL_LINE_PROPS.indexOf(posName) > -1 ? 'left' : 'top';
          const lineValues = this.elementsPos[baseStyleProp].concat(this.viewBox[baseStyleProp]);
          const lineVal = lineValues.find((lineV) => (Math.abs(lineV - elemVal) <= attractGap));
          if (lineVal != null) {
            ret[posName] = {
              lineVal: lineVal,
              elemVal: elemVal,
              offset: lineVal - elemVal,
            };
          }
        });
        return ret;
      },
      // 选择吸附哪条，给出距离该线的偏差，目前线的位置（左右中、上下中）
      abstractOffset() {
        const lines = this.nearLinesInfo;
        const vName = _.find(VERTICAL_LINE_PROPS, (name) => (!!lines[name]));
        const hName = _.find(HOR_LINE_PROPS, (name) => (!!lines[name]));
        const vLine = lines[vName];
        const hLine = lines[hName];

        // 偏移为 0 就无须自动吸附了
        return {
          left: vLine /*&& vLine.offset !== 0*/ ? vLine.offset : null,
          top: hLine /*&& hLine.offset !== 0*/ ? hLine.offset : null,
        };
      }
    },
    methods: {
      posStyle(posName) {
        const lines = this.nearLinesInfo;
        const info = lines && lines[posName];
        const baseStyleProp = VERTICAL_LINE_PROPS.indexOf(posName) > -1 ? 'left' : 'top';
        const ret = { display: 'none' };
        if (info) {
          ret[baseStyleProp] = this.scalePx(info.lineVal);
          ret.display = 'block';
        }
        return ret;
      },
      // 因为 dom 元素高度无法自动响应式，所以通过方法而不是 computed 属性
      updatePositions () {
        this.updateViewBox();
        this.updateElementsBox()
      },
      updateViewBox: function () {
        console.log('>>>updateViewBox');
        var scale = this.scale(1),
          canvasSize = this.canvasSize,
          width = canvasSize.width,
          height = canvasSize.height,
          scaledHeight = height * scale,
          canvasCtHeight = $('.canvas-container').outerHeight(),
          editorOffsetTop = $('.editor').offset().top,
          elemCanvasOffsetTop = $('.element-canvas').offset().top;
        const elemCanvasOverTop =
          editorOffsetTop >= elemCanvasOffsetTop
            ? editorOffsetTop - elemCanvasOffsetTop
            : 0;
        const elemCanvasBottomLineTop =
          scaledHeight - elemCanvasOverTop > canvasCtHeight
            ? elemCanvasOverTop + canvasCtHeight // 如果画布底部超过（可视区）容器底部，bottom 则为 top 偏移 + 可视区（容器）高度
            : scaledHeight; // 如果画布底部在可视区（容器）内，那么 bottom 就是画布高度

        // 考虑可视区位置，如果画布滚动头部超出编辑器，这部分超出就算入 top
        this.viewBox = {
          left: [0, ( 0 + width ) / 2, width],
          top: [
            elemCanvasOverTop / scale,
            (elemCanvasOverTop + elemCanvasBottomLineTop) / scale / 2,
            elemCanvasBottomLineTop / scale
          ]
        };
      },
      updateElementsBox: function () {
        console.log('>>>updateElementsBox');
        var elems = this.elems,
          rotateBox = (this.rotateBox),
          selectedElementId = this.selectedElementId,
          downLineTop = this.viewBox ? this.viewBox.top[2] : this.canvasSize.height,
          upLineTop = this.viewBox ? this.viewBox.top[0] : 0,
          result = {
            left: [],
            top: []
          };
        for (var p in elems) {
          if (elems.hasOwnProperty(p)) {
            const el = elems[p];
            const rotatedStyle = rotateBox(el.style);
            if ("group" != el.type && (
                // 非选中 & 上下基线之间
                rotatedStyle.top <= downLineTop
                && rotatedStyle.top + rotatedStyle.height >= upLineTop
                && el.id != selectedElementId
              )) {
              const { left, top, width, height } = rotatedStyle;
              result.left.push(left, left + parseInt(width / 2), left + width);
              result.top.push(top, parseInt(top + height / 2), top + height);
            }
          }
        }
        this.elementsPos = result;
      }
    },
    watch: {
      canvasSize: {
        deep: true,
        handler: function () {
          this.updatePositions();
        }
      },
      // TODO  updateElementsBox 的依赖都是响应式，可以直接 computed
      selectedElementId: function (id) {
        if (id) {
          this.updateElementsBox();
        }
      },
      'abstractOffset.left': function (left) {
//        console.log('>>> change left left');
        this.$store.commit(T.UPDATE_MOVING_ATTRACT_INFO, { left })
      },
      'abstractOffset.top': function (top) {
//        console.log('>>> change top top');
        this.$store.commit(T.UPDATE_MOVING_ATTRACT_INFO, { top })
      }
    },
    created: function () {
      this.updatePositions();
    },
    beforeCreate: function () {
      this.eventHub.$on(EvTypes.CANVAS_SCROLL_END, () => {
        this.updateViewBox();
      });
    },
  };
</script>


<style lang="scss" rel="stylesheet/scss">
  .line-container {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
    -webkit-user-drag: none;
    pointer-events: none;

    .c-line {
      display: none;
      position: absolute;
      top: 0;
      bottom: 0;
      border-left: 1px solid rgb(0, 255, 238);
    }

    .r-line {
      display: none;
      position: absolute;
      left: 0;
      right: 0;
      border-top: 1px solid rgb(0, 255, 238);
    }
  }
</style>



