<template>
  <div @click="save" class="save btn">
    <span
      v-if="!isSaving && !isIndicateSucc && !isIndicateFail"
      class="icon-save-css fa fa-floppy-o"
    ></span>
    <span v-if="isSaving" class="icon-loading-css fa fa-circle-o-notch"></span>
    <span v-if="isIndicateSucc" class="icon-ok-css fa fa-check"></span>
    <span v-if="isIndicateFail" class="icon-error"></span>
    <span>保存</span>
  </div>
</template>

<script>
  import { getUrlParam } from 'helpers';
  import { mapGetters } from 'vuex';
  import * as T from '../store/types';
  import { Message } from 'element-ui';

  export default {
    data () {
      return {
        isSaving: false,
        isIndicateSucc: false,
        isIndicateFail: false,
      }
    },
    computed: {
      ...mapGetters({}),

    },
    methods: {
      save() {
        // TODO check if change (at least one commit / action)
        // TODO checkLogin
        if (this.isSaving) {
          return;
        }
        this.isSaving = true;
        return this.$store.dispatch(T.SYNC_SAVE_CANVAS)//
          .then(()=> {
            this.isSaving = false;
            this.indicateStatus(true);
            console.log('dispatch(T.SYNC_SAVE_CANVAS) then success')
          }, ()=> {
            this.isSaving = false;
            this.indicateStatus(false);
          })
      },
      indicateStatus(succ) {
        const field = succ ? 'isIndicateSucc' : 'isIndicateFail';
        this[field] = true;
        setTimeout(()=> {
          this[field] = false;
        }, 1000);
        Message({
          message: succ ? '保存成功' : '保存失败',
          type: succ ? 'success' : 'error',
        })
      },
    }

  };


</script>

<style lang="scss" rel="stylesheet/scss">
</style>



