<template>
  <div class="map-element">
    <wrapper
      :element="element"
      :scale-func="scalePx"
      :selected="selectedElementId === elementId"
      :index="index"
      :num-of-elements="numOfElements"
    >
      <map-render :style="renderPosition"
                  :element="element"
                  :is-editor="true"
                  :scale-func="scalePx"
                  class="map-render">
      </map-render>
      <operate
        :style="renderPosition"
        :element-id="elementId"
        :element="element"
        :selected="selected"
        :scale-func="scalePx"
        :show="selected">
      </operate>
    </wrapper>
  </div>
</template>


<script>
  import Base from '../Base';
  import BaseElement from './BaseElement';
  import * as T from '../../store/types';
  import { mapGetters } from 'vuex';
  import Wrapper from './ElementWrapper';
  import Operate from '../Operate';
  import MapRender from './MapRender';

  export default {
    name: 'map-element',
    mixins: [Base, BaseElement],
    props: ['elementId', 'element', 'numOfElements', 'index'],
    components: {
      Wrapper,
      MapRender,
      Operate,
    },
    computed: {
      ...mapGetters({
        selectedElementId: T.GET_SEL_ELEM_ID,
      }),
      // TODO to baseelement
      selected () {
        return this.selectedElementId === this.elementId;
      },
      operatorVisible () {
        return true;
      }
    },
  };
</script>

<style scoped lang="scss" rel="stylesheet/scss">
  .map-element {
    position: absolute;
    .map-render {
      position: absolute;
      top: 50%;
      left: 50%;
    }
  }
</style>



