import {
  INIT_CANVAS_HEIGHT,
  INIT_CANVAS_WIDTH,
} from '../../../constant/index';

/**
 * 初始化的时候会加载单页的数据，
 * 如果是新建的单页就用这份数据作为单页数据
 */
export default {
  // lastModified: 0,
  // editorVersion: 1,
  // version: 1,
  // 可视区的基准大小，放大缩小比例值都以此作为基准计算
  canvasSize: {
    width: INIT_CANVAS_WIDTH,
    height: INIT_CANVAS_HEIGHT,
  },
  background: {
    backgroundColor: '#FFF',
    backgroundImage: false,
    backgroundSize: '50%',
    backgroundRepeat: 'repeat',
  },
  content: []
};



