import Keypress from 'keypress';
import * as EvTypes from '../constant/events';
import Vue from 'vue';


function isActiveTextInput() {
  const activeElement = document.activeElement;
  if (activeElement) {
    const tagName = activeElement.tagName.toUpperCase();
    if (tagName === 'TEXTAREA' || tagName === 'INPUT') {
      return true;
    }
  }
  return false;
}


Vue.mixin({
  created() {
    const captureKeyboard = this.$options.captureKeyboard;
    if (captureKeyboard) {
      this.eventHub.$on(EvTypes.KEYBOARD, this[captureKeyboard]);
    }
  },
  beforeDestroy() {
    const captureKeyboard = this.$options.captureKeyboard;
    if (captureKeyboard) {
      this.eventHub.$off(EvTypes.KEYBOARD, this[captureKeyboard]);
    }
  },
});

export default {
  mounted() {
    const listener = new Keypress.Listener();
    const eventHub = this.eventHub;

    function delKeyboard(name) {
      return {
        keys: [name],
        prevent_default: false,
        on_keydown(e) {
          // 如果不在输入框
          if (!isActiveTextInput() /* || $('.group-element[data-group]').length*/) {
            eventHub.$emit(EvTypes.KEYBOARD, {
              event: e,
              type: 'delete',
            });
            return false;
          }
          return true;
        },
      };
    }

    listener.register_many([
      delKeyboard('delete'),
      delKeyboard('backspace'),
    ]);
  },
};



