<template>
  <div
    v-show="visible"
    class="img-element">
    <wrapper
      :element="element"
      :scale-func="scalePx"
      :selected="selected"
      :index="index"
      :num-of-elements="numOfElements"
    >
      <img-render
        :style="renderPosition"
        :element="element"
        :scale-func="scalePx"
        class="img-render">
      </img-render>
      <operate
        :style="renderPosition"
        :element-id="elementId"
        :element="element"
        :selected="selected"
        :scale-func="scalePx"
        :dbclick-cb="onImgCrop"
        :show="selected"></operate>
    </wrapper>
  </div>
</template>

<script>
  import Wrapper from './ElementWrapper';
  import ImgRender from './ImgRender.vue';
  import Operate from '../Operate';
  import Base from '../Base';
  import { mapGetters } from 'vuex';
  import * as T from '../../store/types';
  import BaseElement from './BaseElement';


  export default {
    props: ['element', 'elementId', 'numOfElements', 'index'],
    mixins: [Base, BaseElement],
    components: {
      Wrapper,
      ImgRender,
      Operate,
    },
    computed: {
      ...mapGetters({
        selectedElementId: T.GET_SEL_ELEM_ID,
      }),
      // TODO to baseelement
      selected () {
        return this.selectedElementId === this.elementId;
      },
      croppingElementId () {
        // map state getter
        return null;
      },
      visible () {
        return true;
//        return this.croppingElementId !== this.elementId && !this.beingGroupSelect
      }
    },
    methods: {
      onImgCrop: function () {

      }
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss">
  .img-element {
    position: absolute;
    .img-render {
      position: absolute;
      top: 50%;
      left: 50%;
    }
  }
</style>



