<template>
  <div
    v-show="selectBox"
    @mousemove="mousemove"
    @mouseup="moveEnd"
    class="select-layer">
    <div
      :style="selectBoxStyle"
      class="select-box">
    </div>
  </div>
</template>

<script>
  import * as EvTypes from '../constant/events';
  import * as T from '../store/types';

  export default {
    created () {
      this.eventHub.$on(EvTypes.EDITOR_MOUSEDOWN, this.editorMousedown);
    },
    data () {
      return {
        selectBox: false
      }
    },
    computed: {
      selectBoxStyle () {
        return null;
      }
    },
    methods: {
      mousemove () {

      },
      moveEnd () {
        // TODO 先啥都不处理，就认为什么都没选中
        const selectedEls = [];
        if (selectedEls.length === 0) {
          this.$store.commit(T.SEL_ELEM, {
            id: null,
            type: 'bg',
          });
        }
        this.selectBox = false
      },
      editorMousedown (e) {
        // TODO padding-wrapper 太一般，重构
        // TODO 判断元素还是太 low，从各个 target 发出 mousedown.self 事件吧
        const triggerTargets = ['cavans-container', 'canvas', 'res-pannel', 'bgCanvas', 'padding-wrapper'];
        const isOneOf = triggerTargets.some(function (cls) {
          return $(e.target).hasClass(cls);
        });
        if (isOneOf) {
          this.selectBox = true;
        }
        // TODO
//        i && (this.$set("mouseY", e.pageY), this.$set("mouseX", e.pageX), this.$set("selectBox", {
//          originEvent: e
//        }))
      }
    }
  };

</script>

<style lang="scss" rel="stylesheet/scss">
  .select-layer {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    cursor: default;

    .select-box {
      position: absolute;
      outline: 1px dashed #00c4cc;
      background: rgba(0, 196, 204, 0.15);
    }
  }
</style>



