<template>
  <div class="canvas-container">
    <div class="padding-wrapper"> <!-- 底部 padding 便于增加画板高度-->
      <v-canvas/>
      <!--<cavans-area -->
      <!--v-if="canvasReady"-->
      <!--:grid-status="gridStatus"-->
      <!--:sub-action="subAction"-->
      <!--:sub-state="subState"-->
      <!--_v-e33f443c="">-->
      <!--</cavans-area>-->
    </div>
    <side-menu/>
    <scaler/>
  </div>
</template>

<script>
  /*
   马卡的实现是 initscroll-> nexttick-> canvasReady 渲染 canvas 面板 -> refresh scroll
   */
  import IScroll from 'iscroll/build/iscroll-probe';
  import Scaler from '../components/Scaler';
  import SideMenu from './SideMenu';
  import { mapGetters } from 'vuex';
  import * as T from '../store/types';
  import vCanvas from './Canvas';
  import * as EvTypes from '../constant/events';

  export default {
    components: {
      vCanvas,
      SideMenu,
      Scaler,
    },
    methods: {
      initScroll () {
        window.myScroll = this.iscroll = new IScroll(this.$el, {
          disableMouse: true, // 禁止拖拽
          mouseWheel: true, // 支持滚轮滚动
          scrollbars: true, // 显示滚动条
          fadeScrollbars: false, // 无需 fade 消失滚动条
          freeScroll: false,
          probeType: 3, // 像素级的触发scroll事件。注意，此时滚动只关注requestAnimationFrame
          scrollX: true, // 允许横向滚动
          interactiveScrollbars: true, // 可以拖动滚动条
        });
        this.iscroll.on('scrollEnd', () => {
          this.eventHub.$emit(EvTypes.CANVAS_SCROLL_END);
        });
      },
    },
    mounted () {
      this.initScroll();
      /*
       单页的逻辑：渲染layout next 初始化滚动，渲染画布元素，next 刷新滚动
       //            this.initScroll();
       //            this.canvasReady = true; // 渲染画布中的元素
       //            this.$nextTick(()=> {
       //              window._iscroll.refresh(); // 元素增加了，刷新滚动条
       //              resolve();
       //            });
       */
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss">
  .canvas-container {
    background: #efefef;
    position: absolute;
    left: 360px;
    top: 50px;
    right: 300px;
    bottom: 0;
    padding: 30px 0 150px 0;
    overflow: hidden;

    .padding-wrapper {
      padding-bottom: 400px;
    }
    .iScrollHorizontalScrollbar,
    .iScrollVerticalScrollbar {
      z-index: 999 !important;
    }
  }
</style>



