<!--
  <div
    :style="wrapperStyle"
    :element-id="elementId"
    @mousedown.self="clearSelect"
    class="wrapper"
    >
    -->
<template>
  <div
    class="wrapper"
    :style="wrapperStyle"
  >
    <slot>

    </slot>
  </div>

</template>


<script>
  import * as EvTypes from '../../constant/events';
  import * as T from '../../store/types';
  import Base from '../Base';


  export default {
    name: 'wrapper',
    props: ["element", "scaleFunc", "selected", "index", "numOfElements"],
    captureKeyboard: 'onKeyboard',
    mixins: [ Base ],
    computed: {
      elementId: function () {
        return this.element.id
      },
//      groupId: function() {
//        return this.groupElement ? this.groupElement.id : void 0
//      },
      wrapperStyle: function () {
        const scaleFunc = this.scaleFunc;
        const rotatedCoord = this.rotateBox(this.element.style);
        return {
          top: scaleFunc(rotatedCoord.top),
          left: scaleFunc(rotatedCoord.left),
          width: scaleFunc(rotatedCoord.width),
          height: scaleFunc(rotatedCoord.height)
        }
      },
    },
    methods: {
      onKeyboard ({ type }) {
        if (this.selected) {
          switch (type) {
            case 'delete':
              this.deleteElement();
              break;
          }
        }
      },
      deleteElement: function () {
        const elementId = this.elementId;
//          groupId = this.groupId,
//          groupElement = this.groupElement,
        const ids = [elementId];
//        if (void 0 !== groupElement && "tempSelect" !== groupElement.groupType) {
//          var o = groupElement.elementArray.filter(function(e) {
//            return e != elementId
//          });
//          o.length <= 1 && (ids.push(groupId), o.forEach(function(t) {
//            me.actions.setElement(t, {
//              groupId: void 0
//            })
//          })), groupElement.elementArray = o, this.actions.setElement(groupId, groupElement, {
//            replace: !0
//          })
//        }
        this.$store.commit(T.RM_ELEMS_BY_ID, { ids })
      },
    },
  }
</script>

<style lang="scss" rel="stylesheet/scss">

  .wrapper {
    position: absolute;
    outline: none;
    z-index: 0;
    user-select: none;

  }
</style>



