<template>
  <div class="picLib">
    <!--
    <div class="navBar"><span v-for="(key, item) in navItems" :class="{ active: key == selectNavId }"
                              @click="selectNav(key)" class="navItem">{{item.name}}</span>
      <div class="line"></div>
    </div>
        -->
    <div class="picArea">
      <div v-for="col in cols" class="picCol">
        <div
          v-for="(item, index) in picLibData[selectNavId].data"
          v-if="index%3 === col"
          class="pic-item"
          :class="{uploadBtn: item === 'uploadBtn'}"
          :key="index"
        >
          <image-uploader
            v-if="item === 'uploadBtn'"
            :on-click="onUploaderClick"
            :on-one-done="appendImg"
          ></image-uploader>
          <div v-else="v-else" class="imgArea">
            <enable-add
              :sizepromise="picSizePromise(item)"
              :click-cb="clickAdd"
              :model="model(item)"
            >
              <pic-lib-img :src="item"/>
            </enable-add>
            <div
              class="delete_btn"
              :title="'删除图片'"
              @click.prevent.stop="deleteImage(index)"
            ></div>
          </div>
        </div>
      </div>
      <!--

<div v-show="picLibData[selectNavId].loading" class="loadingBar">
<loading></loading>
</div>
  -->

    </div>

  </div>
</template>

<script>
  import {
    getUserPicList as apiGetUserPicList,
    removePic as apiRemovePic,
  } from 'api';
  import { getImageNaturalSize } from 'helpers';
  import PicLibImg from './PicLibImg';
  import Vue from 'vue';
  import EnableAdd from './EnableAdd.vue';
  import ImageCompressor from 'html5-image-compress';
  import ImageUploader from './ImageUploader';
  import { mapGetters } from 'vuex';
  import * as T from '../../store/types';
  import IMG_MODEL from '../../constant/IMG_MODEL';
  import _ from 'lodash';
  import Base from '../Base';

  export default {
    name: 'pic-lib',
    mixins: [Base],
    components: {
      PicLibImg,
      EnableAdd,
      ImageUploader,
    },
    data () {
      return {
        picLibData: {
          '0': { // TODO 图片列表不该有这玩意，从模板逻辑去处理，例如 (index+1)%col
            loading: false,
            data: ["uploadBtn"]
          },
        },
        cols: [0, 1, 2],
        checkLogin: true, // TODO
        hasLoadMyImages: false,
        selectNavId: 0,
      };
    },
    computed: {
      ...mapGetters({
        canvaSize: T.GET_CANVAS_SIZE,
        selElem: T.GET_SEL_ELEM,
        selElemId: T.GET_SEL_ELEM_ID,
      }),
      myPic() {
        return this.picLibData[0];
      }
    },
    methods: {
      loadMyImages: function () {
        let myPicLibData = this.myPic;
        apiGetUserPicList().then((list) => {
//          myPicLibData.data.splice(1, myPicLibData.data.length - 1);
          myPicLibData.data = [myPicLibData.data[0]].concat(list);
          myPicLibData.loading = false;
          this.hasLoadMyImages = true;
        });
      },
      onUploaderClick(e) {
        // 如果点击上传的时候还没有登录，就调到登录
//        this.judgeLogin().then(function() {
        if (!this.hasLoadMyImages) {
          this.myPic.loading = true;
          this.loadMyImages();
//          this.checkLogin = true;
          // TODO trigger el click to open file dialog
        }
//        })
      },
      deleteImage: function (index) {
//        Y[0].data[e] === t.previewId && t.actions.setElementPreview(void 0, void 0);
        const picId = this.myPic.data[index];
        this.myPic.data.splice(index, 1);
        apiRemovePic(picId);
        // TODO sync
      },
      appendImg (newUrl) {
        // 在原有多个 url 里剔除新增，然后在添加到首位
        const urls = this.myPic.data.filter(existUrl => {
          return existUrl !== newUrl;
        });
        this.myPic.data = urls;
        // TODO 将来 1 -> 0 无用
        this.myPic.data.splice(1, 0, newUrl);
      },
      model(url) {
        const m = _.merge({}, IMG_MODEL);
        m.picId = url;
        return m;
      },
      clickAdd: function (e) {
        // TODO 选中的和将添加的一样，干不干?
        var me = this,
          size = e.size, // 对应 picSizePromise 的返回值
          slugId = 10,//this.state.getIn(["editorStatus", "slugId"]),
          viewCenter = this.getViewCenter(),
          selElem = this.selElem,
          selElemType = selElem ? selElem.type : "";
        let newModel = _.merge({}, e.model, {
          style: {
            width: size.width,
            height: size.height,
            left: parseInt(viewCenter.left - size.width / 2),
            top: parseInt(viewCenter.top - size.height / 2)
          },
          originWidth: size.originWidth,
          originHeight: size.originHeight,
          scaleWidth: size.width,
          scaleHeight: size.height
        });
        if (selElem && 'img' == selElemType /*|| selElemType == 'vote' && selElem.voteType !== 3*/) {

          const selStyle = selElem.style;
          const opacity = selStyle.opacity;
          const borderRadius = selStyle['border-radius'];
          const boxShadow = selStyle['box-shadow'];
          const rotate = selStyle.rotate;
          const top = selStyle.top;
          const left = selStyle.left;
          const selWidth = /*selElemType == 'vote' ? selElem.picStyle.width :*/ selStyle.width;
          const selHeight = /*selElemType == 'vote' ? selElem.picStyle.height :*/ selStyle.height;
          const selElemId = selElem.id;
          const sizeWHRatio = size.width / size.height;
          const selWidthRatioHeight = selWidth / selHeight;
          const newOrgWidth = size.originWidth;
          const newOrgHeight = size.originHeight;
          let newOrgWidthRatioWidth = void 0;
          let newWidth = size.width;
          let newHeight = size.height;
          let deltaWidthHalf = 0;
          let deltaHeightHalf = 0;
          const cropInfo = {
            x: 0,
            y: 0,
            w: newOrgWidth,
            h: newOrgHeight
          };
          //  即将添加的图片 >= 画布选中的图片宽/高
          if (size.width >= selWidth && size.height >= selHeight) {
            newOrgWidthRatioWidth = newOrgWidth / newWidth;
            deltaWidthHalf = parseInt((newWidth - selWidth) / 2);
            deltaHeightHalf = parseInt((newHeight - selHeight) / 2);
            cropInfo.x = parseInt(deltaWidthHalf * newOrgWidthRatioWidth, 10);
            cropInfo.y = parseInt(deltaHeightHalf * newOrgWidthRatioWidth, 10);
            cropInfo.w = parseInt(selWidth * newOrgWidthRatioWidth, 10);
            cropInfo.h = parseInt(selHeight * newOrgWidthRatioWidth, 10);
          } else {
            if (sizeWHRatio > selWidthRatioHeight) {
              newHeight = selHeight;
              newWidth = newHeight * sizeWHRatio;
              deltaWidthHalf = parseInt((newWidth - selWidth) / 2);
              newOrgWidthRatioWidth = newOrgWidth / newWidth;
              cropInfo.x = parseInt(deltaWidthHalf * newOrgWidthRatioWidth, 10);
              cropInfo.w = parseInt(selWidth * newOrgWidthRatioWidth, 10);
            } else {
              newWidth = selWidth;
              newHeight = newWidth / sizeWHRatio;
              deltaHeightHalf = parseInt((newHeight - selHeight) / 2);
              newOrgWidthRatioWidth = newOrgWidth / newWidth;
              cropInfo.y = parseInt(deltaHeightHalf * newOrgWidthRatioWidth, 10);
              cropInfo.h = parseInt(selHeight * newOrgWidthRatioWidth, 10);
            }
          }
          /*
           if ("vote" == selElemType) {
           _.merge(newModel, {
           picId: model.picId,
           originWidth: newOrgWidth,
           originHeight: newOrgHeight
           })
           }
           */
          _.merge(newModel, {
            id: selElemId,
            scaleWidth: newWidth,
            scaleHeight: newHeight,
            inTop: deltaHeightHalf,
            inLeft: deltaWidthHalf,
            cropData: cropInfo,
            style: {
              opacity,
              borderRadius,
              boxShadow,
              rotate,
              top,
              left,
              width: selStyle.width,
              height: selStyle.height
            }
          });
          this.$store.commit(T.UPDATE_ELEM, newModel);
        } else {
          this.$store.commit(T.ADD_ELEMENT, {
            model: newModel,
            addingWay: 'click',
            autoSelect: true,
          });
        }
      },
      picSizePromise: function (imgUrl) {
        var me = this;
        return new Promise(function (resolve, reject) {
          const canvasWidth = me.canvaSize.width;
          getImageNaturalSize(imgUrl).then((imgNaturalSize) => {
            const imgNaturalWidth = imgNaturalSize.width;
            const imgNaturalHeight = imgNaturalSize.height;
            let width = imgNaturalWidth;
            let height = imgNaturalHeight;
            if (imgNaturalWidth > canvasWidth) {
              width = canvasWidth;
              height = imgNaturalHeight * canvasWidth / imgNaturalWidth;
            }
            resolve({
              width: width,
              height: height,
              originWidth: imgNaturalWidth,
              originHeight: imgNaturalHeight,
              scaleWidth: imgNaturalWidth, // TODO 这里还没计算？？
              scaleHeight: imgNaturalHeight // TODO 这里还没计算？？
            });
          });
        });// ef new Promise
      },
    },

    created: function () {
//      this.loadSystemImages(),
//      c["default"].isUserOnline() && (this.$set("checkLogin", true),
      this.myPic.loading = true;
      this.loadMyImages();
    },
  }
</script>

<style lang="scss" rel="stylesheet/scss">
  .picLib {
    font-size: 13px;
    .item {
      width: 33%;
      img {
        width: 100%;
        user-select: none;
      }
    }
  }

  .navBar {
    width: 100%;
    height: 120px;
    padding-left: 13px;
    .line {
      border-bottom: 1px solid #052b29;
      width: 269px;
      top: 150px;
      left: 5px;
      position: relative;
    }
  }

  .picCol {
    width: 33.33%;
    top: 0;
    float: left;
  }

  .pic-item {
    width: 80px;
    float: left;
    text-align: center;
    margin-top: 10px;
    margin-left: 7px;
    display: inline;
    border: 2px solid rgba(0, 0, 0, 0);
    cursor: pointer;

    &:hover {
      border: 2px solid #02c2cd;
    }
    &:focus {
      //border: 2px solid $important-button-bg;
      outline: none;
      cursor: pointer;
    }
    &.uploadBtn {
      border: 1px dashed #fff;
      &:hover {
        border-color: #02c2cd;
        .add {
          color: #02c2cd;
        }
      }
    }
    .imgArea {
      width: 100%;
      position: relative;
      height: auto;
      img {
        outline: none;
        height: auto;
        max-width: 86px;
        max-height: 200px;
      }
      &:hover {
        .delete_btn {
          opacity: 1;
          background-image: url(http://res.maka.im/cdn/makal/release/a044401b3114480e6520d502aed6a022.png);
          background-size: cover;
          background-repeat: no-repeat;
          visibility: visible;
        }
      }
      .delete_btn {
        position: absolute;
        top: 0;
        left: 0;
        width: 18px;
        height: 18px;
        visibility: hidden;
        disabled: disabled;
        opacity: 0;
        -webkit-transition: all linear 0.5s 0.3s;
        transition: all linear 0.5s 0.3s;
        &:hover {
          cursor: pointer;
          disabled: enabled;
        }
      }
    }
    .uploadBtn {
      position: relative;
      width: 80px;
      height: 80px;
      background: #373f42;
      text-align: center;
      color: #fff;
      .add {
        // color: $text-level-2;
        &:hover {
          cursor: pointer;
          // color: $important-button-bg;
        }
        i {
          display: block;
          line-height: 60px;
        }
        .add-title {
          position: absolute;
          left: 0px;
          bottom: 2px;
          width: 100%;
          height: 40px;
          line-height: 40px;
          text-align: center;
        }
      }
    }
  }

  .loadingBar {
    width: 100%;
    height: 100%;
    text-align: center;
    .loading {
      display: inline-block;
      margin-top: 36px;
    }
  }

  .navItem {
    display: block;
    float: left;
    width: auto;
    padding: 0 12px;
    height: 22px;
    line-height: 22px;
    border-radius: 10px;
    text-align: center;
    margin: 14px 1px 0px 1px;
    color: #fff;
    cursor: pointer;
    &.active {
      //background: $button-background;
      color: #02c2cd;
    }
  }

  .picArea {
    box-sizing: border-box;
    font-size: 12px;
    width: 100%;
    padding: 0px 5px;
    position: absolute;
    /*bottom: 0;*/
    /*top: 155px;*/
  }

</style>



