<!--
包装素材面板中某个元素，让它可以通过 拖拽/点击 来添加元素到面板上
-->
<!--
<div draggable="true" @dragstart="onDragStart" @click="onClickAdd" class="drag-add">
    <slot _v-407ac3ce=""></slot>
</div>'
-->
<template>
  <div class="drag-add" @click="onClickAdd">
    <slot/>
  </div>
</template>

<script>
  export default {
    props: [
      'dragendCb',
      'clickCb',
      'sizepromise',
      'model',
      'colorpromise'
    ],
    methods: {
      sizepromiseProcess: function (e, cb) {
        this.sizepromise.then(function (obj) {
          if (obj.height) {
            cb(obj);
          }
//          else {
//            var obj = {}, jEl = $(e.target);
//            jEl = jEl.find("img").length ? jEl.find("img").eq(0) : jEl;
//            obj.width = obj.width;
//            obj.height = jEl.height() * obj.width / jEl.width();
//            cb(obj);
//          }
        })
      },
      onClickAdd: function (e) {
        this.sizepromiseProcess(e, (size)=> {
          this.clickCb({
            model: this.model,
            colorpromise: this.colorpromise,
            size: size
          })
        })
      }
    }

  }
</script>



