<template>
  <el-dialog ref="dialog" class="preview-layer" v-model="visible" size="full">
    <div class="main">
      <div class="page">
        <iframe :style="iframeStyle" :src="viewerUrl"></iframe>
      </div>
      <div class="info">
        <setting-form class="setting-form"></setting-form>
        <div class="qcode">
          <div class="desc">手机扫一扫即可浏览</div>
          <qrcode :url="viewerUrl" :style="qrcodeSize"></qrcode>
          <div class="url">{{viewerUrl}}</div>
        </div>
      </div>
    </div>
  </el-dialog>
</template>

<script>
  import Vue from 'vue';
  import { getViewerUrl } from '../../../../helpers/urlMgr';
  import * as T from '../../store/types';
  import { mapGetters } from 'vuex';
  import SettingForm from '../MetaSettingForm';
  import qrcode from '../../../../components/qrcode';
  import {
    Dialog,
  } from 'element-ui';
  Vue.use(Dialog);

  const IFRAME_WIDTH = 540;

  export default {
    components: {
      SettingForm,
      qrcode,
    },
    data() {
      return {
        visible: true,
        formData: {},
        qrcodeSize: {
          width: '148px',
          height: '148px',
          margin: '0 auto',
        },
      }
    },
    computed: {
      ...mapGetters({
        canvasSize: T.GET_CANVAS_SIZE,
        metaId: T.GET_OWN_ID,
        meta: T.GET_META,
      }),
      viewerUrl() {
        return getViewerUrl(this.metaId);
      },
      iframeStyle() {
        const canvasSize = this.canvasSize;
        return {
          width: `${IFRAME_WIDTH}px`,
          height: `${IFRAME_WIDTH * canvasSize.height / canvasSize.width}px`
        }
      }
    },
    mounted() {
      this.$refs.dialog.$on('close', this.onClose)
    },
    beforeDestroy() {
      this.$refs.dialog.$off('close', this.onClose)
    },
    methods: {
      onClose() {
        this.$store.commit(T.SET_PREVIEW_VISIBLE, false);
      },
    }
  };
</script>

<style lang="scss" rel="stylesheet/scss">
  .preview-layer {
    z-index: 999999 !important;
    .el-dialog {
      background-color: rgba(241, 241, 239, 0.95);
    }
    .main {
      position: relative;
      width: 980px;
      margin: auto;
      overflow: hidden;
    }

    .page {
      background: #fff;
      box-shadow: 0px 1px 4px 0px #d6d6d6;
      border-radius: 3px;
      width: 600px;
      margin-top: 1px;
      overflow: hidden;
      position: relative;
      padding: 30px 0;
      float: left;
      margin-bottom: 60px;
      pointer-events: none;
      iframe {
        display: block;
        margin: 0 auto;
        border: none;
      }
    }

    .info {
      background: #fff;
      box-shadow: 0px 1px 4px 0px #d6d6d6;
      border-radius: 3px;
      margin-top: 1px;
      margin-left: 20px;
      overflow: hidden;
      position: relative;
      width: 270px;
      float: left;
      box-sizing: border-box;
      .setting-form {
        padding: 20px;
      }
      .qcode {
        box-sizing: border-box;
        padding: 10px 20px;
        position: relative;
        width: 100%;
        border-top: 1px dashed #d6d6d6;
        font-family: PingFangSC-Regular;
        .desc {
          font-size: 16px;
          color: #4a4a4a;
          text-align: center;
          margin-top: 30px;
          margin-bottom: 20px;
        }
        .url {
          font-size: 13px;
          color: #83817b;
          letter-spacing: 0px;
          text-align: center;
          margin-top: 14px;
          text-decoration: underline;
          word-wrap: break-word;
        }
      }
    }
  }
</style>



