import _ from 'lodash';
import Base from './Base';
import * as Types from '../store/types';

export default {
  mixins: [Base],
  data() {
    return {
      scaleObj: null,
      angleMap: {
        n: { x: 0, y: 1, angle: 0 },
        ne: { x: 1, y: 1, angle: 45 },
        e: { x: 1, y: 0, angle: 90 },
        se: { x: 1, y: -1, angle: 135 },
        s: { x: 0, y: -1, angle: 180 },
        sw: { x: -1, y: -1, angle: 225 },
        w: { x: -1, y: 0, angle: 270 },
        nw: { x: -1, y: 1, angle: 315 },
      },
    };
  },
  methods: {
    getCenter() {
      const style = this.eleData.style;
      return {
        x: (style.width / 2) + style.left,
        y: (style.height / 2) + style.top,
      };
    },
    // 12 点为 0 度，元素如果是正方形，则 45度 * n，如果是长方形...
    resetDirectionVectors(width, height) {
      const angle = Math.atan(width / height) / Math.PI * 180;
      this.angleMap.ne.angle = angle;
      this.angleMap.se.angle = 180 - angle;
      this.angleMap.sw.angle = 180 + angle;
      this.angleMap.nw.angle = 360 - angle;
    },
    startScale(e, directtion) {
      console.log('>>>startScale')
      e.stopPropagation();
      const eleData = this.eleData;
      const style = eleData.style;
      const scaleObj = {
        direction: directtion,
        originalStyle: _.merge({}, style, {
          inLeft: eleData.inLeft,
          inTop: eleData.inTop,
          scaleWidth: eleData.scaleWidth,
          scaleHeight: eleData.scaleHeight,
          wr: (eleData.inLeft + (style.width / 2)) / eleData.scaleWidth,
          hr: (eleData.inTop + (style.height / 2)) / eleData.scaleHeight,
        }),
        center: this.getCenter(),
        startPoint: {
          left: e.pageX,
          top: e.pageY,
        },
      };
      this.resetDirectionVectors(style.width, style.height);
      this.scaleObj = scaleObj;
    },
    scaling(e) {
      console.log('>>>scaling');
      e.stopPropagation();
      if (this.scaleObj) {
        this.processScale({
          left: e.pageX,
          top: e.pageY,
        });
      }
    },
    processScale(e) {
      const cos = this.cos;
      const scaleObj = this.scaleObj;
      // (
      //     this.circlePoint,
      //       this.minimun,
      //       this.maximum,
      //       this.Pythagorean,
      //       this.vectorMultij),
      const startPoint = scaleObj.startPoint;
      const rotate = this.rotate ? this.rotate === 360 ? 0 : this.rotate : 0;
      const scale = this.scale(1);
      const direction = scaleObj.direction;
      let s = direction;
      const originalStyle = scaleObj.originalStyle;
      const orgTop = originalStyle.top;
      const orgLeft = originalStyle.left;
      let orgWidth = originalStyle.width;
      let orgHeight = originalStyle.height;
      const moveDeltaLeft = (e.left - startPoint.left) / scale;
      const moveDeltaTop = (e.top - startPoint.top) / scale;
      const moveEndVectorPoint = {
        x: moveDeltaLeft,
        y: -moveDeltaTop,
      };
      const center = scaleObj.center;
      let directionAngle = void 0;
      const angleMap = this.angleMap;
      let vectorDeg = this.vectorMulti(angleMap.n, moveEndVectorPoint);
      if (moveEndVectorPoint.x < 0) {
        vectorDeg = 360 - vectorDeg;
      }
      directionAngle = (rotate + angleMap[direction].angle) % 360;
      const E = cos(moveEndVectorPoint, vectorDeg - directionAngle);
      switch (direction) {
        case 'n':
        case 's':
          s = `${direction}e`;
          break;
        case 'w':
        case 'e':
          s = `n${direction}`;
      }
      let x = (rotate + angleMap[s[1]].angle) % 360;
      x = directionAngle - x;
      const deltaWith = E * Math.cos(x * Math.PI / 180);
      let M = (rotate + angleMap[s[0]].angle) % 360;
      M = directionAngle - M;
      const deltaHeight = E * Math.cos(M * Math.PI / 180);
      const centerXDelta = E * Math.cos((directionAngle - 90) * Math.PI / 180);
      const centerYDelta = E * Math.cos((directionAngle - 180) * Math.PI / 180);
      const centerPoint = {
        x: center.x + (centerXDelta / 2),
        y: center.y + (centerYDelta / 2),
      };
      orgWidth += deltaWith;
      orgHeight += deltaHeight;
      const orgSize = {
        height: orgHeight,
        width: orgWidth,
      };
      const orgPoint = {
        x: orgLeft + (orgWidth / 2),
        y: orgTop + (orgHeight / 2),
      };
      this.fittingCenter(direction, centerPoint, orgPoint, orgSize);
    },
    fittingCenter(direction, centerPoint, orgPoint, orgSize) {
      const r = orgSize;
      const eleData = _.merge({}, this.eleData);
      let picResult;

      r.left = centerPoint.x !== orgPoint.x
        ? centerPoint.x - (r.width / 2)
        : eleData.style.left;

      r.top = centerPoint.y !== orgPoint.y
        ? centerPoint.y - (r.height / 2)
        : eleData.style.top;

      eleData.style.width = Number(r.width.toFixed(2));
      eleData.style.height = Number(r.height.toFixed(2));
      eleData.style.left = Number(r.left.toFixed(2));
      eleData.style.top = Number(r.top.toFixed(2));

      if (this.eleData.type === 'img') {
        picResult = this.picProcess(eleData, direction);
        eleData.inLeft = parseInt(picResult.inLeft, 10);
        eleData.inTop = parseInt(picResult.inTop, 10);
        eleData.scaleWidth = parseInt(picResult.scaleWidth, 10);
        eleData.scaleHeight = parseInt(picResult.scaleHeight, 10);
        eleData.cropData = {
          x: parseInt(eleData.originWidth * picResult.inLeft / picResult.scaleWidth, 10),
          y: parseInt(eleData.originHeight * picResult.inTop / picResult.scaleHeight, 10),
          w: parseInt(eleData.originWidth * eleData.style.width / picResult.scaleWidth, 10),
          h: parseInt(eleData.originHeight * eleData.style.height / picResult.scaleHeight, 10),
        };
      }
      this.$store.commit(Types.UPDATE_ELEM, eleData);
      this.onScaling && this.onScaling();
      // this.actions.__setElementFunc[this.elementId](eleData)
    },

    picProcess(elemData, direction) {
      var style = elemData.style,
        originalStyle = this.scaleObj.originalStyle,
        wr = originalStyle.wr,
        hr = originalStyle.hr,
        orgScaleWidth = originalStyle.scaleWidth,
        orgScaleHeight = originalStyle.scaleHeight,
        orgW = originalStyle.width,
        orgH = originalStyle.height,
        orgInLeft = originalStyle.inLeft,
        orgInTop = originalStyle.inTop,
        scaleW = void 0,
        scaleH = void 0,
        inLeft = void 0,
        inTop = void 0,
        styleH = style.height,
        styleW = style.width,
        styleToOrgWidthRatio = void 0,
        styleToOrgHeightRatio = void 0;
      switch (direction) {
        case 'n':
        case 's':
          scaleH = styleH / 2 / (hr > 0.5 ? (1 - hr) : hr);
          scaleW = scaleH * orgScaleWidth / orgScaleHeight;
          inLeft = (scaleW * wr) - (styleW / 2);
          inTop = (scaleH * hr) - (styleH / 2);
          if (inLeft < 0 || scaleW - inLeft < styleW) {
            scaleW = styleW / 2 / (wr > 0.5 ? (1 - wr) : wr);
            scaleH = scaleW * orgScaleHeight / orgScaleWidth;
            inLeft = (scaleW * wr) - (styleW / 2);
            inTop = (scaleH * hr) - (styleH / 2);
          }
          break;
        case 'w':
        case 'e':
          scaleW = styleW / 2 / (wr > 0.5 ? (1 - wr) : wr);
          scaleH = scaleW * orgScaleHeight / orgScaleWidth;
          inLeft = (scaleW * wr) - (styleW / 2);
          inTop = (scaleH * hr) - (styleH / 2);
          if (inTop < 0 || scaleH - inTop < styleH) {
            scaleH = styleH / 2 / (hr > 0.5 ? (1 - hr) : hr );
            scaleW = scaleH * orgScaleWidth / orgScaleHeight;
            inLeft = (scaleW * wr) - (styleW / 2);
            inTop = (scaleH * hr) - (styleH / 2);
          }
          break;
        default:
          styleToOrgWidthRatio = styleW / orgW;
          styleToOrgHeightRatio = styleH / orgH;
          scaleW = orgScaleWidth * styleToOrgWidthRatio;
          scaleH = orgScaleHeight * styleToOrgHeightRatio;
          inLeft = orgInLeft * styleToOrgWidthRatio;
          inTop = orgInTop * styleToOrgHeightRatio;
      }
      return {
        inLeft,
        inTop,
        scaleWidth: scaleW,
        scaleHeight: scaleH,
      };
    },

    endScale(e) {
      if (this.scaleObj) {
        e.stopPropagation();
        const eleData = _.merge({}, this.eleData);
        const style = eleData.style;
        style.width = parseInt(style.width, 10);
        style.height = parseInt(style.height, 10);
        style.left = parseInt(style.left, 10);
        style.top = parseInt(style.top, 10);
        this.$store.commit(Types.UPDATE_ELEM, eleData);
        this.scaleObj = null;
        // this.informGroupToResize();
      }
    },
  },
};



