import * as T from './types';
import _ from 'lodash';
import Vue from 'vue';

export default {
  [T.SET_PREVIEW_VISIBLE](state, bool) {
    state.previewVisible = bool;
  },

  [T.UPDATE_ELEM_SIZE](state, { id, width, height }){
    _.merge(state.elems[id].style, { width, height });
  },

  [T.UPDATE_ELEM_POS](state, { id, left, top }) {
    _.merge(state.elems[id].style, { left, top });
  },

  [T.SET_OWN_ID](state, id) {
    state.ownId = id;
  },

  [T.SET_FOLK_ID](state, id) {
    state.folkId = id;
  },

  [T.MERGE_META](state, obj) {
    _.merge(state.meta, obj);
  },

  [T.SET_META](state, obj) {
    state.meta = obj;
  },

  [T.SET_GRID_LINE_VISIBLE](state, bool) {
    state.isGridLineVisible = bool;
  },

  [T.SET_CANVAS_SIZE](state, { width, height }) {
    if (width) {
      state.canvasSize.width = width;
    }
    if (height) {
      state.canvasSize.height = height;
    }
  },

  [T.SET_EDITING_ELEMENT_ID](state, id) {
    state.editingElementId = id;
  },

  [T.SET_IS_PIC_LIB_UPLOADING](state, bool) {
    state.picLibUploading = bool;
  },

  [T.SET_IS_ELEM_MOVING](state, bool) {
    // console.log('>>>SET_IS_ELEM_MOVING')
    state.isElemMoving = bool;
    // 初始化吸附相关信息
    if (!bool) {
      _.merge(state.attractMovingInfo, {
        left: null,
        top: null,
      });
    }
  },

  [T.UPDATE_MOVING_ATTRACT_INFO](state, info) {
    _.merge(state.attractMovingInfo, info);
  },

  [T.SEL_ELEM](state, { id, type }) {
    state.selectedElemId = id;
    state.controlPannelType = type;
  },

  [T.RM_ELEMS_BY_ID](state, { ids }) {
    const selectedId = state.selectedElemId;
    ids.forEach((id) => {
      const index = state.elemIdArr.indexOf(id);
      if (index > -1) {
        delete state.elems[id];
        state.elemIdArr.splice(index, 1);
        if (id === selectedId) {
          state.selectedElemId = null;
        }
      }
      // TODO 选中元素如果包含在删除元素，那么要取消选中相关 state
    });
  },

  [T.UPDATE_ELEM](state, elem) {
    _.merge(state.elems[elem.id], elem);
  },
  [T.UPDATE_SEL_ELEM](state, obj) {
    console.log('>>>T.UPDATE_SEL_ELEM', obj)
    const id = state.selectedElemId;
    if (id) {
      _.merge(state.elems[id], obj);
    }
  },

  // payload {model: {}, addingWay: 'click'/'drag'}
  [T.ADD_ELEMENT](state, payload) {
    const model = payload.model;
    const nextId = state.nextId;
    model.id = nextId;
    Vue.set(state.elems, nextId, model);
    Vue.set(state.elemIdArr, state.elemIdArr.length, nextId);
    state.nextId += 1;
  },

  [T.INIT_CANVAS](state, pageData) {
    console.log(`${T.INIT_CANVAS}`, pageData);
    function convertPageData(data) {
      const elems = {};
      const elemIdArr = (data.content || []).map((item)=> {
        const id = parseInt(item.id, 10);
        elems[id] = item;
        return id;
      });
      delete data.content;
      data.elemIdArr = elemIdArr;
      data.elems = elems;
      return data;
    }

    const obj = convertPageData(pageData);
    _.assign(state, obj);
    if (obj.elemIdArr.length) {
      state.nextId = Math.max(...obj.elemIdArr) + 1;
    }
  },

  [T.SET_CANVAS_DATA](state, data) {
    console.log(`${T.SET_CANVAS_DATA}`, data);
    Object.assign(state, data);
    if (data.elemIdArr.length) {
      state.nextId = Math.max(...data.elemIdArr) + 1;
    }
  },

  [T.SET_SCALE_VAL](state, scale) {
    console.log(`${T.SET_SCALE_VAL}`, scale);
    state.scale = scale;
  },

  [T.SET_PRE_CHOOSE_OBJ](state, preChooseObj) {
    state.preChooseObj = preChooseObj;
  },
};



