<template>
  <div id="app">
    <!--<div>{{name}}</div>-->
    <!--<test :name="name"></test>-->
    <h1 v-if="!loaded" align="center">加载中。。。</h1>
    <top-header v-if="loaded"></top-header>
    <editor v-if="loaded"></editor>
  </div>
</template>

<script>
  import store from './store';
  import * as T from './store/types';
  import TopHeader from './components/TopHeader';
  import Editor from './components/Editor';
  import DEFAULT_PAGE_DATA from './constant/default-page-data';

  export default {
    name: 'app',
    store,
    components: {
      TopHeader,
      Editor,
    },
    data () {
      return {
        name: 'peter',
        loaded: false,
        canvasReady: false,
      }
    },
    methods: {
      fetchPageData () {
        // 如果 url 带有单页 id 信息则远程获取，否则（新建）使用默认的初始化数据
        return new Promise((resolve)=> {
          setTimeout(function () {
            resolve(DEFAULT_PAGE_DATA);
          }, 1000);
        });
      },
      init () {
        this.$store.dispatch(T.INIT_DATA_FROM_URL)//
          .then(()=> {
            this.loaded = true; // 渲染整体布局
          });

//        this.fetchPageData()
//          .then(this.convertPageData) // 数据库数据格式化成我们需要的
//          .then(this.setInitData) // 初始化数据
//          .then(this.prepareCanvas); // 准备画板区域
        // TODO 需不需要清空选中焦点（选中背景）
      },
    },
    created () {
      console.log('>>> <App> created');
      // 加载单页数据
      this.init();
    },
    mounted () {
      $('.editor-header')
        .on('move', function (e) {
          console.log('move')
          // move .mydiv horizontally
//          jQuery(this).css({ left: e.startX + e.deltaX });
        })
    }
  };
</script>

<style>
  @keyframes rotate {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }

  .drag-add {
    position: relative;
    user-select: none;
    cursor: pointer;
    overflow: hidden;
  }

  body {
    font-family: sans-serif, Microsoft YaHei, STHeitiSC-Light;
    -webkit-font-smoothing: antialiased;
    background: #fff;
    margin: 0;
  }

  .btn {
    display: inline-block;
    margin: auto 12px;
    height: 34px;
    line-height: 34px;
    border-radius: 5px;
    text-align: center;
    cursor: pointer;
  }

  .spinning {
    animation: rotate 0.8s linear infinite;
  }

</style>



