<template>
  <div class="meta-setting">
    <div class="title-wrap">
      <span @click="showSetting" class="title">{{title}}</span>
      <span @click="showSetting" class="icon-setting-css fa fa-cog"></span>
    </div>
    <el-dialog title="作品设置" v-model="visible" size="tiny">
      <setting-form
        :visible="visible"
        :on-save-success="onSaveSuccess"
      ></setting-form>
    </el-dialog>
  </div>

</template>

<script>
  import _ from 'lodash';
  import { mapGetters } from 'vuex';
  import * as T from '../store/types';
  import Vue from 'vue';
  import META_DEFAULT from '../constant/default-meta';
  import {
    Dialog,
    Button,
    Message,
    Form,
  } from 'element-ui';

  Vue.use(Dialog);
  Vue.use(Button);
  Vue.use(Form);
  import SettingForm from './MetaSettingForm';

  export default {
    components: {
      SettingForm,
    },
    data() {
      return {
        visible: false
      };
    },
    computed: {
      ...mapGetters({
        meta: T.GET_META,
      }),
      title() {
        return this.meta ? this.meta.title : META_DEFAULT.title;
      },
    },
    methods: {
      showSetting() {
        this.visible = true;
      },
      onSaveSuccess() {
        this.visible = false;
      },
    },
  };

</script>

<style lang="scss" rel="stylesheet/scss">
  .meta-setting {
    position: absolute;
    left: 50%;
    top: 50%;
    margin-left: -90px;
    margin-top: -15px;
    width: auto;
    height: 30px;
    line-height: 30px;
    text-align: center;
    color: #fff;
    cursor: pointer;
    .title-wrap{
      opacity: 0.9;
    }
    .title {
      display: inline-block;
      overflow: hidden;
      text-overflow: ellipsis;
      max-width: 300px;
      vertical-align: middle;
      white-space: nowrap;
    }
    .icon-setting-css {
      position: relative;
      right: -10px;
      vertical-align: middle;
    }
    .v-modal{
      opacity: 0.6;
    }
  }
</style>



