<template>
  <div class="text-panel pannel">
    <div class="panel-title border-bottom">文字</div>
    <div class="panel-content">
      <el-form label-position="left" label-width="40px" onsubmit="return false;">
        <el-form-item label="跳转">
          <el-input v-model="formData.href" @change="handleHrefChange" placeholder="输入跳转 URL"></el-input>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue';
  import ElForm from 'element-ui';
  import ElInput from 'element-ui';
  import * as T from '../../store/types';
  import { mapGetters } from 'vuex';
  import _ from 'lodash';

  import {
    MAP_ZOOM_MAX,
    MAP_ZOOM_MIN,
  } from 'constant';


  Vue.use(ElForm);
  Vue.use(ElInput);

  export default {
    components: {},
    data() {
      return {
        formData: {
          href: ''
        }
      };
    },
    computed: {
      ...mapGetters({
        selElemId: T.GET_SEL_ELEM_ID,
        selElem: T.GET_SEL_ELEM,
      }),
      // TODO getters,  or add type:xx config to xx-ctrl.js ?
      selTextElem() {
        const elem = this.selElem;
        return elem && elem.type === 'text' ? elem : null;
      },
    },
    methods: {
      syncFromElem() {
        if(!this.selTextElem) {
          return;
        }
        console.log('>>> syncFromElem');
        const elem = this.selTextElem;
        const data = {};
        if (elem) {
          data.href = elem.href;
        }
        _.merge(this.formData, data);
      },
      handleHrefChange: _.debounce(function (val) {
        this.$store.commit(T.UPDATE_SEL_ELEM, {
          href: val
        })
      }, 350),
    },
    created(){
      this.syncFromElem()
    },
    watch: {
      selTextElem() {
        this.syncFromElem();
      },
    },
  }
</script>

<style lang="scss" rel="stylesheet/scss">

</style>



