<template>
  <el-form onsubmit="return false;" label-position="left" label-width="40px">
    <el-form-item label="标题">
      <el-input v-model="formData.title"></el-input>
    </el-form-item>
    <el-form-item label="描述">
      <el-input type="textarea" v-model="formData.content"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click.native.prevent="handleSubmit" :loading="updating">
        <span v-show="!updating">保存</span>
        <span v-show="updating" :loading="true">保存中</span>
      </el-button>
    </el-form-item>
  </el-form>
</template>

<script>
  import Vue from 'vue';
  import _ from 'lodash';
  import { mapGetters } from 'vuex';
  import * as T from '../store/types';

  import {
    Dialog,
    Button,
    Message,
    Form,
  } from 'element-ui';
  Vue.use(Dialog);
  Vue.use(Button);
  Vue.use(Form);

  export default {
    props: ['visible', 'onSaveSuccess'],
    data(){
      return {
        formData: {},
        updating: false
      };
    },
    computed: {
      ...mapGetters({
        meta: T.GET_META,
      }),
    },
    methods: {
      handleSubmit() {
        if (this.updating) {
          return;
        }
        this.updating = true;
        this.$store.dispatch(T.SYNC_UPDATE_META, this.formData)//
          .then(() => {
            Message.success({ message: '保存成功' });
            this.updating = false;
            this.settingVisible = false;
            this.onSaveSuccess && this.onSaveSuccess();
          }, () => {
            Message.error({ message: '保存失败' });
            this.updating = false;
          });
      },
      syncFromStore() {
        _.forOwn(this.meta, (v, k) => {
          this.$set(this.formData, k, v)
        });
      },
    },
    created(){
      this.syncFromStore();
    },
    watch: {
      visible(isVisible) {
        console.log('changexxxx', arguments)
        this.syncFromStore()
      }
    },
  };
</script>

<style lang="scss" rel="stylesheet/scss">
</style>



