<template>
  <div class="editor" @mousedown="mousedown" @mouseup="mouseup">
    <res-panel/>
    <canvas-container/>
    <select-layer/>
    <ctrl-panel/>
    <preview-layer v-if="previewVisible"/>
  </div>
</template>

<script>

  import ResPanel from './resource/ResPanel.vue';
  import CanvasContainer from './CanvasContainer';
  import { mapGetters } from 'vuex';
  import * as T from '../store/types';
  import { getDomEventPath } from 'helpers';
  import _ from 'lodash';
  import * as EvTypes from '../constant/events';
  import SelectLayer from './SelectLayer';
  import KeyBoard from './EditorKeyboard';
  import CtrlPanel from './control/CtrlPanel';
import PreviewLayer from './preview/PreviewLayer';

  export default {
    mixins: [ KeyBoard ],
    components: {
      ResPanel,
      CanvasContainer,
      SelectLayer,
      PreviewLayer,
      CtrlPanel,
    },
    computed: {
      ...mapGetters({
        preChooseObj: T.GET_PRE_CHOOSE_OBJ,
        previewVisible: T.IS_PREVIEW_VISIBLE,
      })
    },
    methods: {
      /**
       * 检测点击的元素是否属于素材区的一个元素，如果不是，则清空 preChooseObj
       */
      checkPreObj (e) {
        console.log('>>>checkPreObj');
        const preChooseObj = this.preChooseObj;
        if (preChooseObj) {
          const type = preChooseObj.type;
          const domPath = _.dropRight(getDomEventPath(e), 4);
          const isLibItem = domPath.some(({ className }) => {
            return (className && (className + '').indexOf(type) > -1);
          });
          if (!isLibItem) {
            setTimeout(() => {
              this.$store.commit(T.SET_PRE_CHOOSE_OBJ, null);
            })
          }
        }
      },
      mousedown: function (e) {
        if (1 === e.which) {
          var preChooseObj = this.preChooseObj;
          if (preChooseObj) {
//            var type = preChooseObj.type;
//            this.isGroupItem = "group-item" == type;
            this.checkPreObj(e);
          }
          this.eventHub.$emit(EvTypes.EDITOR_MOUSEDOWN, e);
        }
      },
      mouseup: function (e) {
//        this.isGroupItem && this.checkPreObj(e)
      },
    }
  };
</script>



