import Vue from 'vue';
import Vuex from 'vuex';
import actions from './actions';
import * as getters from './getters';
import mutations from './mutations';
import preChooseObj from './subscibe/preChooseObj';
import autoSelect from './subscibe/autoSelect';
// import createLogger from '../../../src/plugins/logger';

Vue.use(Vuex);

// const debug = process.env.NODE_ENV !== 'production'
const state = {
  background: {},
  elemIdArr: [],
  elems: {},
  canvasSize: {},
  //---------------
  isFetching: true,
  editingElementId: null,
  scale: 0.5,
  nextId: 1, // 从 1 开始，不从 0 开始，方便判断
  dragObj: null,
  selectBox: null,
  preChooseObj: null,
  //-----------------
  controlPannelType: 'bg', // 控制面板，例如背景 'bg'
  selectedElemId: null, // 选中画布中的元素
  //---------------
  isElemMoving: null, // 元素正在移动
  attractMovingInfo: {
    left: null,
    top: null,
  }, // 元素吸附时元素相对于吸附线的偏移
  //-------------
  picLibUploading: false, // 是否有图片素材正在上传
  isGridLineVisible: false, // 是否显示画板网格线
  //----
  meta: null, // 作品除页面元素以外的信息，如 名称、描述、标签...，里面的 ID 可能是别人的
  ownId: null, // 自己作品 ID （相当于作品标识 ID）
  folkId: null, // 打开别人作品时，该作品 ID
  //---
  previewVisible: false, // 是否开启预览
};

export default new Vuex.Store({
  // strict: process.env.NODE_ENV !== 'production',
  plugins: [preChooseObj, autoSelect],
  state,
  mutations,
  actions,
  getters,
  modules: {},
  // strict: debug,
  // plugins: debug ? [createLogger()] : []
});



