<template>
  <div class="pannel">
    <div class="panel-title border-bottom">按钮</div>
    <div class="panel-content">
      <el-form label-position="left" label-width="40px" onsubmit="return false;">
        <el-form-item label="电话">
          <el-input v-model="formData.tel" @change="handleTelChange"></el-input>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue';
  import ElForm from 'element-ui';
  import ElInput from 'element-ui';
  import * as T from '../../store/types';
  import { mapGetters } from 'vuex';
  import _ from 'lodash';

  import {
    MAP_ZOOM_MAX,
    MAP_ZOOM_MIN,
  } from 'constant';


  Vue.use(ElForm);
  Vue.use(ElInput);

  export default {
    components: {},
    data() {
      return {
        formData: {
          tel: ''
        }
      };
    },
    computed: {
      ...mapGetters({
        selElemId: T.GET_SEL_ELEM_ID,
        selElem: T.GET_SEL_ELEM,
      }),
      // TODO getters,  or add type:xx config to xx-ctrl.js ?
      selLinkBtnElem() {
        const elem = this.selElem;
        return elem && elem.type === 'linkButton' ? elem : null;
      },
    },
    methods: {
      syncFromElem() {
        if(!this.selLinkBtnElem) {
          return;
        }
        console.log('>>> syncFromElem');
        const elem = this.selLinkBtnElem;
        const data = {};
        if (elem) {
          if (elem.linkType === 'tel') {
            data.tel = elem.tel
          }
        }
        _.merge(this.formData, data);
      },
      handleTelChange: _.debounce(function (val) {
        this.$store.commit(T.UPDATE_SEL_ELEM, {
          tel: val
        })
      }, 350),
    },
    created(){
      this.syncFromElem()
    },
    watch: {
      selLinkBtnElem() {
        this.syncFromElem();
      },
    },
  }
</script>

<style lang="scss" rel="stylesheet/scss">
  $zoomBtnSize: 32px;
  .zoom-btn {
    display: inline-block;
    width: $zoomBtnSize;
    height: $zoomBtnSize;
    line-height: $zoomBtnSize;
    background-color: #fff;
    border-radius: 3px;
    position: relative;
    margin-right: 8px;
    font-size: $zoomBtnSize/2;
    font-weight: 300;
    cursor: pointer;
    text-align: center;
  }
</style>



